import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IWidgetUpdateDialog } from '../../../interfaces/wix.interface';

@Component({
  selector: 'app-widget-image-update-dialog',
  templateUrl: './widget-image-update-dialog.component.html',
  styleUrl: './widget-image-update-dialog.component.scss', standalone: false,
})
export class WidgetImageUpdateDialogComponent implements IWidgetUpdateDialog {
  
  properties: any; // IWidgetUpdateDialog ì¸í„°í˜ì´ìŠ¤ ìš”êµ¬ì‚¬í•­
  imageSrc: string = '';
  imageAlt: string = '';

  constructor(
    private dialogRef: MatDialogRef<WidgetImageUpdateDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ){
    this.properties = data.properties || {};
    this.imageSrc = this.data.properties?.source?.src || '';
    this.imageAlt = this.data.properties?.source?.alt || '';
  }

  save(){
    this.dialogRef.close({
      source: {
        src: this.imageSrc,
        alt: this.imageAlt
      }
    });
  }

  close(){
    this.dialogRef.close();
  }
}
