import { Component, ElementRef, EventEmitter, HostListener, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';
import { IImageProps } from '../../../interfaces/wix.interface';
import { ConfirmDialogComponent } from '../../../../../shared/components/confirm-dialog/confirm-dialog.component';
import { WidgetImageUpdateDialogComponent } from '../../../dialogs/widget-update-dialogs/widget-image-update-dialog/widget-image-update-dialog.component';
import { ImageGalleryDialogComponent } from '../../../dialogs/image-gallery-dialog/image-gallery-dialog.component';
import { take } from 'rxjs';

@Component({
  selector: 'app-widget-image-design',
  templateUrl: './widget-image-design.component.html',
  styleUrl: './widget-image-design.component.scss',
  standalone: false,
})
export class WidgetImageDesignComponent
  extends WidgetComponentAbstract
  implements OnChanges
{
  @Output()
  updateProperties = new EventEmitter<any>();

  @ViewChild('fileInput') fileInput!: ElementRef<HTMLInputElement>;

  style: any = {};
  menuData = [
    {
      icon: 'edit',
      label: 'ì´ë¯¸ì§€ ìˆ˜ì •',
      param: 'edit',
    },
    {
      icon: 'image',
      label: 'ì´ë¯¸ì§€ ë³€ê²½',
      param: 'changeImage',
    },
    {
      icon: 'upload',
      label: 'íŒŒì¼ ì—…ë¡œë“œ',
      param: 'uploadImage',
    },
    {
      icon: 'photo_library',
      label: 'ê°¤ëŸ¬ë¦¬ì—ì„œ ì„ íƒ',
      param: 'selectFromGallery',
    },
    {
      icon: 'close',
      label: 'ìœ„ì ¯ ì œê±°',
      param: 'delete',
    },
  ];

  ngOnChanges(changes: SimpleChanges): void {
    this.style = { ...this.getStyle() };
  }

  getDefaultProperties() {
    return {
      style: {
        rounded: {
          enabled: false,
          label: 'ëª¨ì„œë¦¬ ë‘¥ê¸€ê²Œ(px)',
          cssProperty: 'border-radius',
          value: 0,
          unit: 'px',
          updateValue: true,
          exclusiveKey: 'circled',
        },
        // modal: {enabled: false, label: 'ëª¨ë‹¬ë¡œ ë„ìš°ê¸°'},
        circled: {
          enabled: false,
          label: 'ì›í˜• ì´ë¯¸ì§€',
          cssProperty: 'border-radius',
          value: '50%',
          updateValue: false,
          exclusiveKey: 'rounded',
        },
        blackWhite: {
          enabled: false,
          label: 'í‘ë°± ì´ë¯¸ì§€',
          cssProperty: 'filter',
          value: 'grayscale(100%)',
          updateValue: false,
        },
        width: {
          enabled: true,
          label: 'ë„ˆë¹„(px)',
          cssProperty: 'width',
          value: 300,
          unit: 'px',
          updateValue: true,
          ckeckable: false,
        },
        height: {
          enabled: true,
          label: 'ë†’ì´(px)',
          cssProperty: 'height',
          value: 200,
          unit: 'px',
          updateValue: true,
          ckeckable: false,
        },
      },
      source: {
        src: '',
        alt: '',
      },
      description: '',
      link: '',
      animation: {},
    } as IImageProps;
  }

  getStyle() {
    let styles: { [key: string]: string | number } = {};

    for (const key in this.properties?.style) {
      const item = this.properties.style[key];
      if (!item.enabled) continue; // âœ… í™œì„±í™”ëœ í•­ëª©ë§Œ ì¶”ê°€

      styles[item.cssProperty] = item.unit
        ? `${item.value}${item.unit}`
        : item.value;
    }

    return styles;
  }

  onMenuSelect(event: any) {
    console.log('ì´ë²¤íŠ¸ ë°œìƒ, ', event);
    (this as any)[event]();
  }

  delete() {
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      data: {
        title: 'ìœ„ì ¯ ì‚­ì œ',
        message: 'ì •ë§ë¡œ ì´ ì´ë¯¸ì§€ ìœ„ì ¯ì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?'
      }
    });
    dialogRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((result) => {
        if (!result) return;
        this.remove();
      });
  }

  edit() {
    const dialogRef = this.openUpdateDialog(WidgetImageUpdateDialogComponent);
    dialogRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((properties: any) => {
        console.log('í´ë¡œì¦ˆ, ', properties);
        if (!properties) {
          console.log('ì•„ë¬´ ë°ì´í„°ë„ ì•ˆë„˜ì–´ì˜´, ', properties);
          return;
        }
        this.properties = { ...properties };
        this.updateWidgetProperties(this.properties);
      });
  }

  changeImage() {
    const url = prompt('ì´ë¯¸ì§€ URLì„ ì…ë ¥í•˜ì„¸ìš”:', this.properties.source?.src || '');
    if (url !== null) {
      this.updateWidgetProperties({
        ...this.properties,
        source: {
          ...this.properties.source,
          src: url
        }
      });
    }
  }

  uploadImage() {
    this.fileInput.nativeElement.click();
  }

  onFileSelected(event: Event) {
    const input = event.target as HTMLInputElement;
    if (input.files && input.files[0]) {
      const file = input.files[0];
      
      // íŒŒì¼ íƒ€ì… ì²´í¬
      if (!file.type.startsWith('image/')) {
        alert('ì´ë¯¸ì§€ íŒŒì¼ë§Œ ì—…ë¡œë“œ ê°€ëŠ¥í•©ë‹ˆë‹¤.');
        return;
      }

      // íŒŒì¼ í¬ê¸° ì²´í¬ (5MB)
      if (file.size > 5 * 1024 * 1024) {
        alert('íŒŒì¼ í¬ê¸°ëŠ” 5MB ì´í•˜ì—¬ì•¼ í•©ë‹ˆë‹¤.');
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        const base64Image = e.target?.result as string;
        this.updateWidgetProperties({
          ...this.properties,
          source: {
            src: base64Image,
            alt: file.name
          }
        });
      };
      reader.readAsDataURL(file);
    }
  }

  selectFromGallery() {
    const dialogRef = this.dialog.open(ImageGalleryDialogComponent, {
      width: '90vw',
      maxWidth: '1200px',
      height: '80vh'
    });

    dialogRef.afterClosed().pipe(take(1)).subscribe((selectedImage: any) => {
      if (selectedImage) {
        this.updateWidgetProperties({
          ...this.properties,
          source: {
            src: selectedImage.src?.large || selectedImage.src?.medium || selectedImage.url,
            alt: selectedImage.alt || 'Selected image'
          }
        });
      }
    });
  }

  updateWidgetProperties(properties: { [key: string]: any }): void {
    // Widget properties ì—…ë°ì´íŠ¸ ë¡œì§
    this.properties = { ...this.properties, ...properties };
    this.style = this.getStyle();
    this.updateProperties.emit(this.properties);
  }

  onClick() {
    if (!this.properties?.action) return;
    (this as any)[this.properties.action.action]();
  }

  modal() {
    console.log('ëª¨ë‹¬');
  }

  link() {
    console.log('ë§í¬', this.properties.action.value);
  }

  onResize({ width, height }: { width: number; height: number }) {
    console.log('ìƒˆ í¬ê¸°px', width, height);
    this.properties.size.width = width;
    this.properties.size.height = height;

    if (!this.properties.style.width) {
      this.properties.style.width = {
        enabled: true,
        label: 'ë„ˆë¹„',
        cssProperty: 'width',
        value: width,
        unit: 'px',
        updateValue: true,
        ckeckable: false,
      };
    } else {
      this.properties.style.width.value = width;
    }

    if (!this.properties.style.height) {
      this.properties.style.height = {
        enabled: true,
        label: 'ë†’ì´',
        cssProperty: 'height',
        value: height,
        unit: 'px',
        updateValue: true,
        ckeckable: false,
      };
    } else {
      this.properties.style.height.value = height;
    }
    console.log(this.properties);

    this.updateWidgetProperties(this.properties);
  }
}
