import { BehaviorSubject, filter, Subject, take, Observable } from "rxjs";
import { IApp, IPage } from "../interfaces/wix.interface";
import { MOCK_APP_DATA } from "../const/mock-data.const";
import { inject } from "@angular/core";
import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "../../../../environments/environment";
import { CreateAppDTO } from "../dto/create-app.dto";
import { UtilService } from "../../../shared/services/util.service";
import { WixAutoSaveService } from "../services/wix-auto-save.service";

export class WixViewModel {
  appData$ = new BehaviorSubject<IApp | null>(null);
  appDataList$ = new BehaviorSubject<IApp[]>([]);
  selectedPage$ = new BehaviorSubject<IPage | null>(null);
  destroy$ = new Subject<void>();

  private http = inject(HttpClient);
  private utilService = inject(UtilService);
  private autoSaveService = inject(WixAutoSaveService);
  private baseUrl = `${environment.baseSyncUrl}/wix`;

  constructor() {
    // this.localDataInit();
    this.setupAutoSave();
  }

  /**
   * ìë™ ì €ì¥ ì„¤ì •
   */
  private setupAutoSave() {
    // appData ë³€ê²½ ì‹œ ìë™ ì €ì¥
    this.appData$.pipe(
      filter(data => !!data)
    ).subscribe(appData => {
      if (appData) {
        this.autoSaveService.notifyAppDataChange(appData);
      }
    });

    // selectedPage ë³€ê²½ ì‹œ ìë™ ì €ì¥
    this.selectedPage$.pipe(
      filter(data => !!data)
    ).subscribe(pageData => {
      if (pageData) {
        this.autoSaveService.notifyPageDataChange(pageData);
      }
    });
  }

  localDataInit() {
    const localStorageData = localStorage.getItem('wixAppData');
    if (!localStorageData) {
      this.appData$.next(MOCK_APP_DATA);
      this.syncLocalData();
      return;
    }
    const appData = JSON.parse(localStorageData) as IApp;
    this.appData$.next(appData);
    console.log('ë¡œì»¬ìŠ¤í† ë¦¬ì§€ ë°ì´í„°', appData);
    this.syncLocalData();
  }

  serverDataInit(ownerId: string) {
    const params = new HttpParams().set('ownerId', ownerId);

    this.http.get(`${this.baseUrl}/apps`, {params}).pipe(
      take(1)
    ).subscribe((result: any) => {
      console.log('ì¡°íšŒ ê²°ê³¼, ', result);
      const refineData = result.map((app: any) => {
        const {owner, pages, ...rest} = app;
        return {
          ...rest,
          ownerId: owner.id,
          pages: JSON.parse(pages)
        };
      });
      console.log('ì •ì œ ê²°ê³¼, ', refineData);
      this.appDataList$.next(refineData);
    });
  }

  getAppData(appId: string){
    this.http.get(`${this.baseUrl}/apps/${appId}`).pipe(
      take(1)
    ).subscribe((result: any) => {
      console.log('getAppData ì¡°íšŒ ê²°ê³¼, ', result);
      if(!result){
        return;
      }
      const {owner, pages, ...rest} = result;
      const refineData = {
        ...rest,
        ownerId: owner.id,
        pages: JSON.parse(pages),
      };
      console.log('ì •ì œ ê²°ê³¼, ', refineData);
      this.appData$.next(refineData);
    });
  }

  syncAppData(appData: IApp) {
    this.appData$.next(appData);
  }

  syncLocalData() {
    this.appData$.pipe(filter((appData) => !!appData)).subscribe((appData) => {
      localStorage.setItem('wixAppData', JSON.stringify(appData));
    });
  }

  updateAppData(app: IApp){
    const param = {
      ...app,
      pages: JSON.stringify(app.pages)
    };
    this.http.patch(`${this.baseUrl}/apps`, param).pipe(
      take(1)
    ).subscribe(result => {
      console.log(result);
    });
  }

  createApp(param: CreateAppDTO) {
    this.http
      .post(`${this.baseUrl}/apps`, param)
      .pipe(take(1))
      .subscribe((result) => {
        console.log('ìƒì„±ê²°ê³¼, ', result);
        if (param.ownerId) {
          this.serverDataInit(param.ownerId);
        }
      });
  }

  deleteAppData(appId: string, ownerId: string){
    const params = new HttpParams().set('appId', appId);
    this.http.delete(`${this.baseUrl}/apps`, {params}).pipe(
      take(1)
    ).subscribe(result => {
      console.log('ì•± ì œê±° ê²°ê³¼, ', result);
      this.serverDataInit(ownerId);
    });
  }

  // ==================== í˜ì´ì§€ CRUD ====================
  
  /**
   * ìƒˆë¡œìš´ í˜ì´ì§€ ìƒì„±
   */
  createPage(appId: string, pageName: string): void {
    const currentApp = this.appData$.value;
    if (!currentApp) {
      console.warn('í˜„ì¬ ì•± ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const newPage: IPage = {
      id: this.utilService.generateUUID(),
      name: pageName,
      path: `/${pageName.toLowerCase().replace(/\s+/g, '-')}`,
      sections: [],
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };

    const updatedApp: IApp = {
      ...currentApp,
      pages: [...currentApp.pages, newPage],
      updatedAt: new Date().toISOString()
    };

    this.updateAppData(updatedApp);
    this.appData$.next(updatedApp);
    this.utilService.openSnackBar('í˜ì´ì§€ê°€ ìƒì„±ë˜ì—ˆìŠµë‹ˆë‹¤.', 'success');
  }

  /**
   * í˜ì´ì§€ ê°€ì ¸ì˜¤ê¸° (ì•± ë‚´ íŠ¹ì • í˜ì´ì§€)
   */
  getPage(pageId: string): IPage | undefined {
    const currentApp = this.appData$.value;
    if (!currentApp) return undefined;

    return currentApp.pages.find(page => page.id === pageId);
  }

  /**
   * í˜ì´ì§€ ì„ íƒ (í˜„ì¬ í¸ì§‘ ì¤‘ì¸ í˜ì´ì§€ ì„¤ì •)
   */
  selectPage(pageId: string): void {
    const page = this.getPage(pageId);
    if (page) {
      this.selectedPage$.next(page);
    }
  }

  /**
   * í˜ì´ì§€ ì—…ë°ì´íŠ¸
   */
  updatePage(pageId: string, updates: Partial<IPage>): void {
    const currentApp = this.appData$.value;
    if (!currentApp) {
      console.warn('í˜„ì¬ ì•± ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const pageIndex = currentApp.pages.findIndex(page => page.id === pageId);
    if (pageIndex === -1) {
      console.warn('í˜ì´ì§€ë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const updatedPages = [...currentApp.pages];
    updatedPages[pageIndex] = {
      ...updatedPages[pageIndex],
      ...updates,
      updatedAt: new Date().toISOString()
    };

    const updatedApp: IApp = {
      ...currentApp,
      pages: updatedPages,
      updatedAt: new Date().toISOString()
    };

    this.updateAppData(updatedApp);
    this.appData$.next(updatedApp);
    
    // í˜„ì¬ ì„ íƒëœ í˜ì´ì§€ë„ ì—…ë°ì´íŠ¸
    if (this.selectedPage$.value?.id === pageId) {
      this.selectedPage$.next(updatedPages[pageIndex]);
    }
  }

  /**
   * í˜ì´ì§€ ì‚­ì œ
   */
  deletePage(pageId: string): void {
    const currentApp = this.appData$.value;
    if (!currentApp) {
      console.warn('í˜„ì¬ ì•± ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const updatedPages = currentApp.pages.filter(page => page.id !== pageId);
    
    const updatedApp: IApp = {
      ...currentApp,
      pages: updatedPages,
      updatedAt: new Date().toISOString()
    };

    this.updateAppData(updatedApp);
    this.appData$.next(updatedApp);

    // ì‚­ì œëœ í˜ì´ì§€ê°€ í˜„ì¬ ì„ íƒëœ í˜ì´ì§€ë©´ ì´ˆê¸°í™”
    if (this.selectedPage$.value?.id === pageId) {
      this.selectedPage$.next(null);
    }

    this.utilService.openSnackBar('í˜ì´ì§€ê°€ ì‚­ì œë˜ì—ˆìŠµë‹ˆë‹¤.', 'success');
  }

  /**
   * í˜ì´ì§€ ë³µì œ
   */
  duplicatePage(pageId: string): void {
    const currentApp = this.appData$.value;
    if (!currentApp) {
      console.warn('í˜„ì¬ ì•± ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const originalPage = this.getPage(pageId);
    if (!originalPage) {
      console.warn('ë³µì œí•  í˜ì´ì§€ë¥¼ ì°¾ì„ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const duplicatedPage: IPage = {
      ...JSON.parse(JSON.stringify(originalPage)), // Deep copy
      id: this.utilService.generateUUID(),
      name: `${originalPage.name} (ë³µì‚¬ë³¸)`,
      path: `${originalPage.path}-copy`,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };

    const updatedApp: IApp = {
      ...currentApp,
      pages: [...currentApp.pages, duplicatedPage],
      updatedAt: new Date().toISOString()
    };

    this.updateAppData(updatedApp);
    this.appData$.next(updatedApp);
    this.utilService.openSnackBar('í˜ì´ì§€ê°€ ë³µì œë˜ì—ˆìŠµë‹ˆë‹¤.', 'success');
  }

  /**
   * í˜ì´ì§€ ìˆœì„œ ë³€ê²½
   */
  reorderPages(fromIndex: number, toIndex: number): void {
    const currentApp = this.appData$.value;
    if (!currentApp) {
      console.warn('í˜„ì¬ ì•± ë°ì´í„°ê°€ ì—†ìŠµë‹ˆë‹¤.');
      return;
    }

    const updatedPages = [...currentApp.pages];
    const [movedPage] = updatedPages.splice(fromIndex, 1);
    updatedPages.splice(toIndex, 0, movedPage);

    const updatedApp: IApp = {
      ...currentApp,
      pages: updatedPages,
      updatedAt: new Date().toISOString()
    };

    this.updateAppData(updatedApp);
    this.appData$.next(updatedApp);
  }
}
