import { Component, inject, Input, ViewChild } from '@angular/core';
import { IApp } from '../../interfaces/wix.interface';
import { v4 as uuid } from 'uuid';
import { MatDrawer } from '@angular/material/sidenav';
import { MembershipService } from '../../../membership/membership.service';
import { WixViewModel } from '../../viewmodels/wix.viewmodel';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { distinctUntilChanged, filter, map, Subject, takeUntil, tap } from 'rxjs';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-app-render',
  templateUrl: './app-render.component.html',
  styleUrl: './app-render.component.scss',
  standalone: false,
})
export class AppRenderComponent {
  @Input()
  appData: IApp = {
    id: uuid(),
    name: 'ì´ˆê¸°ë°ì´í„°',
    ownerId: '',
    pages: [],
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  };

  @ViewChild('drawer', { static: true, read: MatDrawer })
  drawer!: MatDrawer;

  selectedAppId!: string;

  membershipService = inject(MembershipService);
  sketchVM = inject(WixViewModel);
  dialog = inject(MatDialog);
  router = inject(Router);
  route = inject(ActivatedRoute);
  destroy$ = new Subject<void>();

  toolbarData: any = {
    data: this.appData?.name ?? 'ë¯¸ë¦¬ë³´ê¸°',
    buttonConfigs: [],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };

  sideToolbarData: any = {
    data: 'ë©”ë‰´',
    buttonConfigs: [
      {
        data: {
          icon: 'close',
          tooltip: 'ë©”ë‰´ ë‹«ê¸°',
        },
        style: {},
        event: () => {
          this.drawer.close();
        },
      },
    ],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };
  pageData: any;

  ngOnInit(): void {
    // this.pageData = this.appData.pages[0];

    this.route.paramMap
      .pipe(
        map((params) => {
          const appId = params.get('appId');

          if (!appId) {
            console.log('appId ì•ˆë„˜ì–´ì˜´', appId);
            this.router.navigate(['syncbi/nocode/dashboard']);
          }

          return appId;
        }),
        distinctUntilChanged(), // ì´ì „ appIdì™€ ê°™ìœ¼ë©´ ë¬´ì‹œ
        tap((appId) => {
          if (!appId) return;

          this.sketchVM.getAppData(appId);
        })
      )
      .subscribe((appId) => {
        if (!appId) {
          console.log('appId ì•ˆë„˜ì–´ì˜´', appId);
          this.router.navigate(['syncbi/nocode/dashboard']);
          return;
        }

        this.selectedAppId = appId;
      });

    this.initAppData();

  }

  initAppData(){
    this.sketchVM.appData$
      .pipe(
        // tap((data) => {
        //   if (!data || data.id !== this.selectedAppId) {
        //     this.sketchVM.getAppData(this.selectedAppId); // ì„œë²„ ìš”ì²­
        //   }
        // }),
        filter((data) => {
          return !!data;
        }),
        takeUntil(this.destroy$)
      )
      .subscribe((data) => {
        console.log('ì•± ë°ì´í„°, ', data);
        if (!data) return;
        this.appData = {...data};
        this.toolbarData = {
          ...this.toolbarData,
          data: data.name,
        };

        // this.toolbarData.data = data.name;
        if (data.pages.length > 0) {
          this.pageData = data.pages[0];
        }else{
          this.pageData = undefined;
        }
      });
  }

  toolbarButtonInit() {
    this.toolbarData = {
      ...this.toolbarData,
      buttonConfigs: [
        ...this.toolbarData.buttonConfigs,
        {
          data: {
            icon: 'draw',
            tooltip: 'ë””ìì¸ ëª¨ë“œ',
          },
          style: {},
          event: () => {
            this.router.navigate([
              '/syncbi/nocode/manage/design',
              this.appData.id,
            ]);
          },
        },
        {
          data: {
            icon: 'public',
            tooltip: 'ê²Œì‹œí•˜ê¸°',
          },
          style: {
            label: {
              fontSize: '100px',
            },
          },
          event: () => {
            this.sketchVM.updateAppData(this.appData);
          },
        },
      ],
    };
  }

  onPageChange(page: any) {
    this.pageData = page;
  }
}
