import { Injectable, inject } from '@angular/core';
import { BehaviorSubject, Observable, Subject, debounceTime, distinctUntilChanged, filter } from 'rxjs';
import { IApp, IPage } from '../interfaces/wix.interface';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../environments/environment';

export enum SaveStatus {
  IDLE = 'IDLE',
  SAVING = 'SAVING',
  SAVED = 'SAVED',
  ERROR = 'ERROR'
}

export interface AutoSaveState {
  status: SaveStatus;
  lastSaved?: Date;
  error?: string;
  isPending: boolean;
}

@Injectable({
  providedIn: 'root'
})
export class WixAutoSaveService {
  
  private http = inject(HttpClient);
  private baseUrl = `${environment.baseSyncUrl}/wix`;

  // ì €ì¥ ìƒíƒœ ê´€ë¦¬
  private saveStateSubject = new BehaviorSubject<AutoSaveState>({
    status: SaveStatus.IDLE,
    isPending: false
  });
  public saveState$: Observable<AutoSaveState> = this.saveStateSubject.asObservable();

  // ì•± ë°ì´í„° ë³€ê²½ ìŠ¤íŠ¸ë¦¼
  private appDataChangeSubject = new Subject<IApp>();
  
  // í˜ì´ì§€ ë°ì´í„° ë³€ê²½ ìŠ¤íŠ¸ë¦¼
  private pageDataChangeSubject = new Subject<IPage>();

  // ìë™ ì €ì¥ í™œì„±í™” ì—¬ë¶€
  private autoSaveEnabled = true;
  
  // Debounce ì‹œê°„ (ë°€ë¦¬ì´ˆ)
  private debounceTime = 1000;

  constructor() {
    this.initAutoSave();
    console.log('âœ… WixAutoSaveService ì´ˆê¸°í™”');
  }

  /**
   * ìë™ ì €ì¥ ì´ˆê¸°í™”
   */
  private initAutoSave(): void {
    // ì•± ë°ì´í„° ìë™ ì €ì¥
    this.appDataChangeSubject
      .pipe(
        filter(() => this.autoSaveEnabled),
        debounceTime(this.debounceTime),
        distinctUntilChanged((prev, curr) => JSON.stringify(prev) === JSON.stringify(curr))
      )
      .subscribe(appData => {
        this.saveAppToLocalStorage(appData);
        this.saveAppToServer(appData);
      });

    // í˜ì´ì§€ ë°ì´í„° ìë™ ì €ì¥
    this.pageDataChangeSubject
      .pipe(
        filter(() => this.autoSaveEnabled),
        debounceTime(this.debounceTime),
        distinctUntilChanged((prev, curr) => JSON.stringify(prev) === JSON.stringify(curr))
      )
      .subscribe(pageData => {
        this.savePageToLocalStorage(pageData);
      });
  }

  /**
   * ì•± ë°ì´í„° ë³€ê²½ ì•Œë¦¼
   */
  notifyAppDataChange(appData: IApp): void {
    this.setSaveStatus(SaveStatus.IDLE, true);
    this.appDataChangeSubject.next(appData);
  }

  /**
   * í˜ì´ì§€ ë°ì´í„° ë³€ê²½ ì•Œë¦¼
   */
  notifyPageDataChange(pageData: IPage): void {
    this.setSaveStatus(SaveStatus.IDLE, true);
    this.pageDataChangeSubject.next(pageData);
  }

  /**
   * LocalStorageì— ì•± ë°ì´í„° ì €ì¥
   */
  private saveAppToLocalStorage(appData: IApp): void {
    try {
      localStorage.setItem('wixAppData', JSON.stringify(appData));
      console.log('ğŸ’¾ LocalStorageì— ì•± ì €ì¥ ì™„ë£Œ');
    } catch (error) {
      console.warn('âŒ LocalStorage ì €ì¥ ì‹¤íŒ¨:', error);
      this.setSaveStatus(SaveStatus.ERROR, false, 'ë¡œì»¬ ì €ì¥ ì‹¤íŒ¨');
    }
  }

  /**
   * LocalStorageì— í˜ì´ì§€ ë°ì´í„° ì €ì¥
   */
  private savePageToLocalStorage(pageData: IPage): void {
    try {
      localStorage.setItem('wixPageData', JSON.stringify(pageData));
      console.log('ğŸ’¾ LocalStorageì— í˜ì´ì§€ ì €ì¥ ì™„ë£Œ');
    } catch (error) {
      console.warn('âŒ LocalStorage ì €ì¥ ì‹¤íŒ¨:', error);
      this.setSaveStatus(SaveStatus.ERROR, false, 'ë¡œì»¬ ì €ì¥ ì‹¤íŒ¨');
    }
  }

  /**
   * ì„œë²„ì— ì•± ë°ì´í„° ì €ì¥
   */
  private saveAppToServer(appData: IApp): void {
    this.setSaveStatus(SaveStatus.SAVING, true);

    const payload = {
      ...appData,
      pages: JSON.stringify(appData.pages)
    };

    this.http.patch(`${this.baseUrl}/apps`, payload).subscribe({
      next: (response) => {
        console.log('â˜ï¸ ì„œë²„ì— ì•± ì €ì¥ ì™„ë£Œ:', response);
        this.setSaveStatus(SaveStatus.SAVED, false);
      },
      error: (error) => {
        console.warn('âŒ ì„œë²„ ì €ì¥ ì‹¤íŒ¨:', error);
        this.setSaveStatus(SaveStatus.ERROR, false, error.message || 'ì„œë²„ ì €ì¥ ì‹¤íŒ¨');
      }
    });
  }

  /**
   * LocalStorageì—ì„œ ì•± ë°ì´í„° ë¡œë“œ
   */
  loadAppFromLocalStorage(): IApp | null {
    try {
      const data = localStorage.getItem('wixAppData');
      if (data) {
        const appData = JSON.parse(data) as IApp;
        console.log('ğŸ“‚ LocalStorageì—ì„œ ì•± ë¡œë“œ ì™„ë£Œ');
        return appData;
      }
      return null;
    } catch (error) {
      console.warn('âŒ LocalStorage ë¡œë“œ ì‹¤íŒ¨:', error);
      return null;
    }
  }

  /**
   * LocalStorageì—ì„œ í˜ì´ì§€ ë°ì´í„° ë¡œë“œ
   */
  loadPageFromLocalStorage(): IPage | null {
    try {
      const data = localStorage.getItem('wixPageData');
      if (data) {
        const pageData = JSON.parse(data) as IPage;
        console.log('ğŸ“‚ LocalStorageì—ì„œ í˜ì´ì§€ ë¡œë“œ ì™„ë£Œ');
        return pageData;
      }
      return null;
    } catch (error) {
      console.warn('âŒ LocalStorage ë¡œë“œ ì‹¤íŒ¨:', error);
      return null;
    }
  }

  /**
   * ìˆ˜ë™ ì €ì¥
   */
  manualSave(appData: IApp): void {
    console.log('ğŸ’¾ ìˆ˜ë™ ì €ì¥ ì‹œì‘...');
    this.saveAppToLocalStorage(appData);
    this.saveAppToServer(appData);
  }

  /**
   * ìë™ ì €ì¥ í™œì„±í™”/ë¹„í™œì„±í™”
   */
  setAutoSaveEnabled(enabled: boolean): void {
    this.autoSaveEnabled = enabled;
    console.log(`ğŸ”„ ìë™ ì €ì¥ ${enabled ? 'í™œì„±í™”' : 'ë¹„í™œì„±í™”'}`);
  }

  /**
   * Debounce ì‹œê°„ ì„¤ì •
   */
  setDebounceTime(ms: number): void {
    this.debounceTime = Math.max(100, ms);
    console.log(`â±ï¸ Debounce ì‹œê°„: ${this.debounceTime}ms`);
  }

  /**
   * ì €ì¥ ìƒíƒœ ì—…ë°ì´íŠ¸
   */
  private setSaveStatus(status: SaveStatus, isPending: boolean, error?: string): void {
    this.saveStateSubject.next({
      status,
      lastSaved: status === SaveStatus.SAVED ? new Date() : this.saveStateSubject.value.lastSaved,
      error,
      isPending
    });
  }

  /**
   * LocalStorage ì´ˆê¸°í™”
   */
  clearLocalStorage(): void {
    localStorage.removeItem('wixAppData');
    localStorage.removeItem('wixPageData');
    console.log('ğŸ—‘ï¸ LocalStorage ì´ˆê¸°í™” ì™„ë£Œ');
  }

  /**
   * í˜„ì¬ ì €ì¥ ìƒíƒœ ì¡°íšŒ
   */
  getCurrentSaveState(): AutoSaveState {
    return this.saveStateSubject.value;
  }

  /**
   * ë§ˆì§€ë§‰ ì €ì¥ ì‹œê°„ í¬ë§·
   */
  getLastSavedFormatted(): string {
    const lastSaved = this.saveStateSubject.value.lastSaved;
    if (!lastSaved) return 'ì €ì¥ëœ ì  ì—†ìŒ';

    const now = new Date();
    const diff = now.getTime() - lastSaved.getTime();
    const seconds = Math.floor(diff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);

    if (seconds < 60) return `${seconds}ì´ˆ ì „`;
    if (minutes < 60) return `${minutes}ë¶„ ì „`;
    if (hours < 24) return `${hours}ì‹œê°„ ì „`;
    
    return lastSaved.toLocaleString('ko-KR');
  }

  /**
   * ì €ì¥ ëŒ€ê¸° ì¤‘ì¸ì§€ í™•ì¸
   */
  isSavePending(): boolean {
    return this.saveStateSubject.value.isPending;
  }
}
