import { Component } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';

@Component({
  selector: 'app-widget-spacer',
  templateUrl: './widget-spacer.component.html',
  styleUrl: './widget-spacer.component.scss', standalone: false,
})
export class WidgetSpacerComponent extends WidgetComponentAbstract {
  
  getDefaultProperties() {
    return {
      height: 20
    }
  }

  updateWidgetProperties(properties: { [key: string]: any; }): void {
    this.properties = { ...this.properties, ...properties };
  }
}
