import { Component } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';

@Component({
  selector: 'app-widget-button',
  templateUrl: './widget-button.component.html',
  styleUrl: './widget-button.component.scss', standalone: false,
})
export class WidgetButtonComponent extends WidgetComponentAbstract {
  
  getDefaultProperties() {
    return {
      text: 'Click me',
      link: '',
      color: 'primary'
    }
  }

  updateWidgetProperties(properties: { [key: string]: any; }): void {
    this.properties = { ...this.properties, ...properties };
  }
}
