import { HttpClient } from "@angular/common/http";
import { inject, Injectable } from "@angular/core";
import { BehaviorSubject, take } from "rxjs";
import { PexelsCategory } from "../enums/pexels-category.enum";
import { IPexelsImage } from "../interfaces/image-source.interface";
import { environment } from "../../../../environments/environment";

@Injectable()
export class StaticImageViewModel {
  http = inject(HttpClient);

  // SyncBI-Stationì˜ static ì´ë¯¸ì§€ JSON ì‚¬ìš©
  private baseUrl = `${environment.apiUrl}/static`;
  selectedCategory: PexelsCategory = PexelsCategory.Nature;
  
  // imageSource$ Observable
  imageSource$ = new BehaviorSubject<IPexelsImage[]>([]);

  getImagesByCategory(category: PexelsCategory | string) {
    const url = `${this.baseUrl}/pexels_${category}.json`;
    console.log('ì´ë¯¸ì§€ ìš”ì²­ ê²½ë¡œ:', url);
    this.selectedCategory = category as PexelsCategory;

    this.http
      .get<IPexelsImage[]>(url)
      .pipe(take(1))
      .subscribe({
        next: (images) => {
          this.imageSource$.next(images);
          console.log('ì´ë¯¸ì§€ ìš”ì²­ ì™„ë£Œ:', images.length);
        },
        error: (error) => {
          console.warn('ì´ë¯¸ì§€ ë¡œë“œ ì‹¤íŒ¨:', error);
          this.imageSource$.next([]);
        }
      });
  }

  // ì´ˆê¸° ì´ë¯¸ì§€ ë¡œë“œ
  initImages() {
    this.getImagesByCategory(PexelsCategory.Nature);
  }
}
