import { Component, inject, Input, OnChanges, OnInit, SimpleChanges, ViewChild } from '@angular/core';
import { IApp, IPage } from '../../interfaces/wix.interface';
import { MatDrawer } from '@angular/material/sidenav';
import { MatDialog } from '@angular/material/dialog';
import { PageCreateDialogComponent } from '../../dialogs/page-create-dialog/page-create-dialog.component';
import { WixViewModel } from '../../viewmodels/wix.viewmodel';
import { filter, map, Subject, take, takeUntil, tap } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { PageUpdateDialogComponent } from '../../dialogs/page-update-dialog/page-update-dialog.component';
import { ConfirmDialogComponent } from '../../../../shared/dialogs/confirm-dialog/confirm-dialog.component';
import { v4 as uuid } from 'uuid';

/**
 * ë””ìì¸ í™”ë©´
 */
@Component({
  selector: 'app-app-design',
  templateUrl: './app-design.component.html',
  styleUrl: './app-design.component.scss',
  standalone: false,
})
export class AppDesignComponent implements OnInit {
  appData: IApp = {
    id: uuid(),
    name: 'ì´ˆê¸°ë°ì´í„°',
    ownerId: '',
    pages: [],
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString()
  };

  @ViewChild('drawer', { static: true, read: MatDrawer })
  drawer!: MatDrawer;

  selectedAppId!: string;

  sketchVM = inject(WixViewModel);
  dialog = inject(MatDialog);
  router = inject(Router);
  route = inject(ActivatedRoute);
  destroy$ = new Subject<void>();

  toolbarData: any = {
    data: 'ë””ìì¸ ëª¨ë“œ',
    buttonConfigs: [
      {
        data: {
          icon: 'visibility',
          tooltip: 'ë¯¸ë¦¬ë³´ê¸°',
        },
        style: {},
        event: () => {
          console.log('ë¯¸ë¦¬ë³´ê¸° ì´ë™, ', this.appData.id);
          this.navigate();
        },
      },
      {
        data: {
          icon: 'public',
          tooltip: 'ê²Œì‹œí•˜ê¸°',
        },
        style: {
          label: {
            fontSize: '100px',
          },
        },
        event: () => {
          this.sketchVM.updateAppData(this.appData);
        },
      },
    ],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };
  sideToolbarData: any = {
    data: 'ë©”ë‰´',
    buttonConfigs: [
      {
        data: {
          icon: 'note_add',
          tooltip: 'í˜ì´ì§€ ì¶”ê°€',
        },
        style: {},
        event: () => {
          this.openPageCreateDialog();
        },
      },
      {
        data: {
          icon: 'close',
          tooltip: 'ë©”ë‰´ ë‹«ê¸°',
        },
        style: {},
        event: () => {
          this.drawer.close();
        },
      },
    ],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };
  contextMenu = [
    {
      icon: 'edit',
      label: 'í˜ì´ì§€ ì •ë³´ ìˆ˜ì •',
      param: 'edit',
    },
    {
      icon: 'delete',
      label: 'í˜ì´ì§€ ì œê±°',
      param: 'delete',
    },
  ];
  selectedPage: any;

  ngOnInit(): void {
    // this.pageData = this.appData.pages[0];
    const appId = this.route.snapshot.paramMap.get('appId');
    if (!appId) {
      console.log('appId ì•ˆë„˜ì–´ì˜´', appId);
      this.router.navigate(['syncbi/nocode/dashboard']);
      return;
    }
    this.selectedAppId = appId;
    // this.sketchVM.getAppData(appId);

    this.sketchVM.appData$.pipe(
      tap((data) => {
        if (!data || data.id !== appId) {
          this.sketchVM.getAppData(appId); // ì„œë²„ ìš”ì²­
        }
      }),
      filter((data) => {
        return !!data;
      }),
      takeUntil(this.destroy$)
    )
    .subscribe((data) => {
      console.log('ì•± ë°ì´í„°, ', data);
      if (!data) return;
      this.appData = data;
      // this.toolbarData.data = data.name;
      if (!this.selectedPage && data.pages.length > 0) {
        this.selectedPage = data.pages[0];
      }
    });
  }

  onPageChange(page: any) {
    this.selectedPage = page;
  }

  openPageCreateDialog() {
    const dialogRef = this.dialog.open(PageCreateDialogComponent, {
      width: '50vw',
      // height: '60vh',
      maxWidth: '100vw',
      maxHeight: '100vh',
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log('ë‹¤ì´ì–¼ë¡œê·¸ ë‹«í˜, ', result);
      if (!result) return;
      this.appData.pages.push(result);
      this.checkPageData();
    });
  }

  openPageUpdateDialog(pageData: IPage) {
    const dialogRef = this.dialog.open(PageUpdateDialogComponent, {
      data: pageData,
      width: '50vw',
      // height: '60vh',
      maxWidth: '100vw',
      maxHeight: '100vh',
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log('ë‹¤ì´ì–¼ë¡œê·¸ ë‹«í˜, ', result);
      if (!result) return;
      const targetIndex = this.appData.pages.findIndex(
        (page) => page.id === result.id
      );
      this.appData.pages[targetIndex] = { ...result };
    });
  }

  onItemChange(updatedPage: IPage) {
    const targetIndex = this.appData.pages.findIndex(
      (page) => page.id === updatedPage.id
    );

    // ë²„ê·¸ ë°©ì§€ìš©
    if (targetIndex === -1) {
      console.log('ë§¤ì¹­ë˜ëŠ” ì„¹ì…˜ ì—†ìŒ(í˜„ì¬ í˜ì´ì§€)');
      return;
    }

    this.appData.pages[targetIndex] = { ...updatedPage };
    console.log('ì•± ë°ì´í„° ì—…ë°ì´íŠ¸', this.appData.pages[targetIndex]);
    this.syncData();
  }

  syncData() {
    this.sketchVM.syncAppData(this.appData);
  }

  onMenuSelect(event: any, param?: any) {
    console.log('ì´ë²¤íŠ¸ ë°œìƒ, ', event);
    (this as any)[event](param);
  }

  edit(param?: any) {
    this.openPageUpdateDialog(param);
  }

  delete() {
    const dialogRef = this.dialog.open(ConfirmDialogComponent);
    dialogRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((result) => {
        if (!result) return;
        this.remove();
      });
  }

  remove() {
    const targetIndex = this.appData.pages.findIndex(
      (page) => page.id === this.selectedPage.id
    );
    this.appData.pages.splice(targetIndex, 1);
    console.log('í˜ì´ì§€ ì œê±°, ', this.appData);

    this.selectedPage = this.appData.pages[0];
    this.syncData();
  }

  checkPageData() {
    if (this.appData.pages.length === 1) {
      this.selectedPage = this.appData.pages[0];
    }
  }

  navigate(){
    this.router.navigate(['/syncbi/nocode/manage/preview', this.appData.id,]);
  }
}
