import { Component, EventEmitter, inject, Input, Output } from '@angular/core';
import { IRow, ISection, ISketchComponent, IWidget } from '../../interfaces/wix.interface';
import { MatDialog } from '@angular/material/dialog';
import { WidgetListDialogComponent } from '../../dialogs/widget-list-dialog/widget-list-dialog.component';
import { take } from 'rxjs';
import { v4 as uuid } from 'uuid';
import { ConfirmDialogComponent } from '../../../../shared/dialogs/confirm-dialog/confirm-dialog.component';
import { WidgetType } from '../../enums/widget-type.enum';
import { DEFAUT_WIDGET_CONTEXT_MENU, WIDGET_CONTEXT_MENU_MAP } from '../../const/widget-context-menu.const';
import { IContextMenu } from '../../../../shared/interfaces/context-menu.interface';

@Component({
  selector: 'app-section-design',
  templateUrl: './section.component.html',
  styleUrl: './section.component.scss',
  standalone: false,
})
export class SectionDesignComponent implements ISketchComponent<ISection> {

  @Input()
  data!: ISection;
  @Input()
  sectionIndex!: number;

  @Output()
  changedItem = new EventEmitter<ISection>();
  @Output()
  createSection = new EventEmitter<number>();
  @Output()
  deleteSection = new EventEmitter<number>();

  dialog = inject(MatDialog);

  menuData: IContextMenu[] = (WIDGET_CONTEXT_MENU_MAP['section' as keyof typeof WIDGET_CONTEXT_MENU_MAP] ?? DEFAUT_WIDGET_CONTEXT_MENU) as unknown as IContextMenu[];

  addItem(type?: WidgetType): void {
    if(!type)return;

    const widgetData = this.createWidget(type);
    const rowData = this.createRow(widgetData);
    this.data.rows.push(rowData);
    this.changedItem.emit(this.data);
  }
  removeItem(id: string): void {
    throw new Error('Method not implemented.');
  }

  onItemChange(updatedItem: any): void {
    console.log('sectionì—ì„œ ë°ì´í„° ë³€í™” ê°ì§€',updatedItem);

    const targetIndex = this.data.rows.findIndex(item => item.id === updatedItem.id);

    if(targetIndex === -1) {
      console.log('ë§¤ì¹­ë˜ëŠ” row ì—†ìŒ(í˜„ì¬ ì„¹ì…˜)');
      return;
    }

    const target = this.data.rows[targetIndex];

    if(updatedItem.columns.length === 0){
      console.log('row ë¹„ì–´ìˆìŒ, \n', updatedItem);
      this.data.rows.splice(targetIndex, 1);
      return;
    }

    this.data.rows[targetIndex] = {...updatedItem};
    this.changedItem.emit(this.data);

  }

  onMenuSelect(menu: string){
    console.log('ë©”ë‰´ ì…˜íƒ, ', menu);
    (this as any)[menu]();
  }

  addSection(){
    this.createSection.emit(this.sectionIndex);
  }

  delete(){
    const dialogRef = this.dialog.open(ConfirmDialogComponent);
    dialogRef.afterClosed().pipe(
      take(1),
    ).subscribe(result => {
      if(!result) return;
      this.remove();
    })

  }

  remove(){
    this.deleteSection.emit(this.sectionIndex);
  }


  openWidgetDialog(){
    const dialogRef = this.dialog.open(WidgetListDialogComponent, {
      panelClass: 'widget-dialog'
    });

    dialogRef.afterClosed().pipe(
      take(1)
    ).subscribe(result => {
      this.addItem(result);
    })
  }

  createRow(widget: IWidget): IRow{
    return {
      id: uuid(),
      type: 'row',
      columns: [
        {
          id: uuid(),
          widget
        }
      ]
    }
  }

  createWidget(widgetType: WidgetType):IWidget{
    return {
      id: uuid(),
      type: 'widget',
      widgetType,
    }
  }

}
