import {
  Directive,
  ElementRef,
  EventEmitter,
  HostListener,
  Input,
  Output,
  ComponentRef,
  ViewContainerRef,
  ApplicationRef,
  Injector,
  createComponent
} from '@angular/core';
import { IContextMenu } from '../interfaces/context-menu.interface';
import { ContextMenuComponent } from '../components/context-menu/context-menu.component';

/**
 * ìš°í´ë¦­ ì»¨í…ìŠ¤íŠ¸ ë©”ë‰´ ë””ë ‰í‹°ë¸Œ
 * 
 * ì‚¬ìš©ë²•:
 * <div appWixContextMenu [menuData]="menuData" (selectMenu)="onMenuSelect($event)">
 */
@Directive({
  selector: '[appWixContextMenu]',
  standalone: false,
})
export class ContextMenuDirective {
  @Input() menuData: IContextMenu[] = [];
  @Output() selectMenu = new EventEmitter<any>();

  private menuRef?: ComponentRef<ContextMenuComponent>;

  constructor(
    private el: ElementRef,
    private viewContainer: ViewContainerRef,
    private appRef: ApplicationRef,
    private injector: Injector
  ) {}

  @HostListener('contextmenu', ['$event'])
  onContextMenu(event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();

    // ê¸°ì¡´ ë©”ë‰´ê°€ ìˆìœ¼ë©´ ì œê±°
    this.closeMenu();

    // ë©”ë‰´ ì»´í¬ë„ŒíŠ¸ ìƒì„±
    this.menuRef = createComponent(ContextMenuComponent, {
      environmentInjector: this.appRef.injector,
      elementInjector: this.injector
    });

    // ë©”ë‰´ ìœ„ì¹˜ ë° ë°ì´í„° ì„¤ì •
    this.menuRef.instance.menuData = this.menuData;
    this.menuRef.instance.x = event.clientX;
    this.menuRef.instance.y = event.clientY;

    // ì´ë²¤íŠ¸ êµ¬ë…
    this.menuRef.instance.menuSelect.subscribe((param: any) => {
      this.selectMenu.emit(param);
      this.closeMenu();
    });

    this.menuRef.instance.close.subscribe(() => {
      this.closeMenu();
    });

    // DOMì— ì¶”ê°€
    this.appRef.attachView(this.menuRef.hostView);
    const domElem = (this.menuRef.hostView as any).rootNodes[0] as HTMLElement;
    document.body.appendChild(domElem);
  }

  private closeMenu(): void {
    if (this.menuRef) {
      this.appRef.detachView(this.menuRef.hostView);
      this.menuRef.destroy();
      this.menuRef = undefined;
    }
  }

  ngOnDestroy(): void {
    this.closeMenu();
  }
}
