import { Component, EventEmitter, Input, Output } from '@angular/core';

export interface StyleConfig {
  backgroundColor?: string;
  color?: string;
  fontSize?: number;
  fontFamily?: string;
  fontWeight?: string;
  fontStyle?: string;
  textAlign?: string;
  padding?: string;
  margin?: string;
  borderRadius?: number;
  borderWidth?: number;
  borderColor?: string;
  borderStyle?: string;
}

@Component({
  selector: 'app-style-editor-panel',
  templateUrl: './style-editor-panel.component.html',
  styleUrl: './style-editor-panel.component.scss',
  standalone: false
})
export class StyleEditorPanelComponent {
  
  @Input() style: StyleConfig = {};
  @Input() showTextOptions = true;
  @Input() showBackgroundOptions = true;
  @Input() showBorderOptions = true;
  @Input() showSpacingOptions = true;

  @Output() styleChange = new EventEmitter<StyleConfig>();

  // í°íŠ¸ íŒ¨ë°€ë¦¬ ì˜µì…˜
  fontFamilies = [
    { label: 'Pretendard', value: 'Pretendard, sans-serif' },
    { label: 'Noto Sans KR', value: '"Noto Sans KR", sans-serif' },
    { label: 'Roboto', value: 'Roboto, sans-serif' },
    { label: 'Arial', value: 'Arial, sans-serif' },
    { label: 'Georgia', value: 'Georgia, serif' },
    { label: 'Courier New', value: '"Courier New", monospace' },
  ];

  // í°íŠ¸ í¬ê¸° ì˜µì…˜
  fontSizes = [10, 12, 14, 16, 18, 20, 24, 28, 32, 36, 48, 64, 72];

  // ì •ë ¬ ì˜µì…˜
  alignments = [
    { icon: 'format_align_left', value: 'left', label: 'ì™¼ìª½ ì •ë ¬' },
    { icon: 'format_align_center', value: 'center', label: 'ê°€ìš´ë° ì •ë ¬' },
    { icon: 'format_align_right', value: 'right', label: 'ì˜¤ë¥¸ìª½ ì •ë ¬' },
    { icon: 'format_align_justify', value: 'justify', label: 'ì–‘ìª½ ì •ë ¬' },
  ];

  // ë³´ë” ìŠ¤íƒ€ì¼ ì˜µì…˜
  borderStyles = [
    { label: 'ì‹¤ì„ ', value: 'solid' },
    { label: 'ì ì„ ', value: 'dotted' },
    { label: 'íŒŒì„ ', value: 'dashed' },
    { label: 'ì´ì¤‘ì„ ', value: 'double' },
  ];

  // í”„ë¦¬ì…‹ ìƒ‰ìƒ
  presetColors = [
    '#000000', '#FFFFFF', '#FF0000', '#00FF00', '#0000FF',
    '#FFFF00', '#FF00FF', '#00FFFF', '#808080', '#C0C0C0',
    '#800000', '#008000', '#000080', '#808000', '#800080',
    '#008080', '#FFA500', '#FFC0CB', '#A52A2A', '#DEB887'
  ];

  updateStyle(key: keyof StyleConfig, value: any) {
    this.style = { ...this.style, [key]: value };
    this.styleChange.emit(this.style);
  }

  toggleBold() {
    const newWeight = this.style.fontWeight === 'bold' ? 'normal' : 'bold';
    this.updateStyle('fontWeight', newWeight);
  }

  toggleItalic() {
    const newStyle = this.style.fontStyle === 'italic' ? 'normal' : 'italic';
    this.updateStyle('fontStyle', newStyle);
  }

  setAlignment(align: string) {
    this.updateStyle('textAlign', align);
  }
}
