import { Component, EventEmitter, Output } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';
import { ConfirmDialogComponent } from '../../../../../shared/components/confirm-dialog/confirm-dialog.component';
import { take } from 'rxjs';

@Component({
  selector: 'app-widget-text-design',
  templateUrl: './widget-text-design.component.html',
  styleUrl: './widget-text-design.component.scss',
  standalone: false,
})
export class WidgetTextDesignComponent extends WidgetComponentAbstract {

  @Output()
  updateProperties = new EventEmitter<any>();

  // í¸ì§‘ ëª¨ë“œ í”Œë˜ê·¸
  isEditing = false;
  
  // ì„ì‹œ í¸ì§‘ ê°’
  tempText = '';

  menuData = [
    {
      icon: 'edit',
      label: 'í…ìŠ¤íŠ¸ ìˆ˜ì •',
      param: 'edit'
    },
    {
      icon: 'format_size',
      label: 'í°íŠ¸ í¬ê¸°',
      param: 'fontSize'
    },
    {
      icon: 'palette',
      label: 'ìƒ‰ìƒ ë³€ê²½',
      param: 'color'
    },
    {
      icon: 'format_align_left',
      label: 'ì •ë ¬',
      param: 'align'
    },
    {
      icon: 'close',
      label: 'ìœ„ì ¯ ì œê±°',
      param: 'delete'
    },
  ]

  override ngOnInit(): void {
    super.ngOnInit();
    // ê¸°ë³¸ê°’ ì„¤ì •
    if (!this.properties) {
      this.properties = this.getDefaultProperties();
    }
  }

  override getDefaultProperties() {
    return {
      text: 'í…ìŠ¤íŠ¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”',
      fontSize: 16,
      color: '#000000',
      fontWeight: 'normal',
      fontStyle: 'normal',
      textAlign: 'left',
      fontFamily: 'Pretendard, sans-serif'
    };
  }

  override updateWidgetProperties(properties: { [key: string]: any }): void {
    this.properties = { ...this.properties, ...properties };
    this.updateProperties.emit(this.properties);
  }

  onMenuSelect(event: any){
    console.log('ë©”ë‰´ ì„ íƒ: ', event);
    (this as any)[event]();
  }

  delete(){
    const dialogRef = this.dialog.open(ConfirmDialogComponent, {
      data: {
        title: 'ìœ„ì ¯ ì‚­ì œ',
        message: 'ì •ë§ë¡œ ì´ í…ìŠ¤íŠ¸ ìœ„ì ¯ì„ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?'
      }
    });
    dialogRef.afterClosed().pipe(
      take(1),
    ).subscribe(result => {
      if(!result) return;
      this.remove();
    })
  }

  edit(){
    this.isEditing = true;
    this.tempText = this.properties.text || '';
    // ë‹¤ìŒ í‹±ì—ì„œ í¬ì»¤ìŠ¤ ì„¤ì •
    setTimeout(() => {
      const textarea = document.querySelector('.text-editor') as HTMLTextAreaElement;
      if (textarea) {
        textarea.focus();
        textarea.select();
      }
    }, 0);
  }

  saveText(){
    if (this.tempText.trim()) {
      this.updateWidgetProperties({ text: this.tempText });
    }
    this.isEditing = false;
  }

  cancelEdit(){
    this.isEditing = false;
    this.tempText = '';
  }

  onDoubleClick(){
    this.edit();
  }

  fontSize(){
    const currentSize = this.properties.fontSize || 16;
    const sizes = [12, 14, 16, 18, 20, 24, 28, 32, 36, 48, 64];
    const currentIndex = sizes.indexOf(currentSize);
    const nextIndex = (currentIndex + 1) % sizes.length;
    this.updateWidgetProperties({ fontSize: sizes[nextIndex] });
  }

  color(){
    // ê°„ë‹¨í•œ ìƒ‰ìƒ ë³€ê²½ (ì¶”í›„ ì»¬ëŸ¬ í”¼ì»¤ë¡œ ëŒ€ì²´)
    const colors = ['#000000', '#FF0000', '#00FF00', '#0000FF', '#FFFF00', '#FF00FF', '#00FFFF'];
    const currentColor = this.properties.color || '#000000';
    const currentIndex = colors.indexOf(currentColor);
    const nextIndex = (currentIndex + 1) % colors.length;
    this.updateWidgetProperties({ color: colors[nextIndex] });
  }

  align(){
    const alignments = ['left', 'center', 'right', 'justify'];
    const currentAlign = this.properties.textAlign || 'left';
    const currentIndex = alignments.indexOf(currentAlign);
    const nextIndex = (currentIndex + 1) % alignments.length;
    this.updateWidgetProperties({ textAlign: alignments[nextIndex] });
  }

  toggleBold(){
    const currentWeight = this.properties.fontWeight || 'normal';
    this.updateWidgetProperties({ 
      fontWeight: currentWeight === 'bold' ? 'normal' : 'bold' 
    });
  }

  toggleItalic(){
    const currentStyle = this.properties.fontStyle || 'normal';
    this.updateWidgetProperties({ 
      fontStyle: currentStyle === 'italic' ? 'normal' : 'italic' 
    });
  }

}
