import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { WixLayoutComponent } from "./layout/wix-layout/wix-layout.component";
import { WixLoginComponent } from "./components/wix-login/wix-login.component";
import { WixAuthGuard } from "../../guards/wix-auth.guard";

const routes: Routes = [
  {
    path: 'login',
    component: WixLoginComponent
  },
  {
    path: '',
    component: WixLayoutComponent,
    canActivate: [WixAuthGuard]
  }
];

@NgModule({
  declarations: [],
  imports: [
    RouterModule.forChild(routes)
  ]
})
export class WixRoutingModule {

}
