import { Component } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';

@Component({
  selector: 'app-widget-text',
  templateUrl: './widget-text.component.html',
  styleUrl: './widget-text.component.scss', standalone: false,
})
export class WidgetTextComponent extends WidgetComponentAbstract {
  
  getDefaultProperties() {
    return {
      text: 'Sample text',
      fontSize: 16,
      color: '#000000'
    }
  }

  updateWidgetProperties(properties: { [key: string]: any; }): void {
    this.properties = { ...this.properties, ...properties };
  }
}
