import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IColumn } from '../../interfaces/wix.interface';

@Component({
  selector: 'app-column',
  templateUrl: './column.component.html',
  styleUrl: './column.component.scss', standalone: false,
})
export class ColumnComponent {
  @Input() data!: IColumn;

  @Output()
  changedItem = new EventEmitter<IColumn>();

  onWidgetChange(updatedWidget: any){
    this.data.widget = updatedWidget;
    this.changedItem.emit(this.data);
  }

  onRowChange(updatedRow: any){
    if(!this.data.rows) return;
    
    const targetIndex = this.data.rows.findIndex(row => row.id === updatedRow.id);
    if(targetIndex === -1) return;

    this.data.rows[targetIndex] = updatedRow;
    this.changedItem.emit(this.data);
  }
}
