import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { IPage, ISection } from '../../interfaces/wix.interface';
import { v4 as uuid } from 'uuid';

@Component({
  selector: 'app-page-design',
  templateUrl: './page.component.html',
  styleUrl: './page.component.scss',
  standalone: false,
})
export class PageDesignComponent implements OnInit, OnChanges {
  @Input() data!: IPage;

  @Output()
  changedItem = new EventEmitter<IPage>();

  ngOnChanges(changes: SimpleChanges): void {
    console.log('í˜ì´ì§€ ë³€ê²½', this.data);

    if (this.data.sections.length === 0) {
      this.addSection(-1);
    }
  }

  ngOnInit(): void {
    // if (this.data.sections.length === 0) {
    //   this.addSection(-1);
    // }
  }

  addSection(sectionIndex: number) {
    console.log('ì„¹ì…˜ ì¶”ê°€', sectionIndex);

    const newSection = this.createSection();
    this.data.sections.splice(sectionIndex + 1, 0, newSection);
  }

  deleteSection(sectionIndex: number) {
    this.data.sections.splice(sectionIndex, 1);
  }

  onItemChange(updatedItem: any) {
    const targetIndex = this.data.sections.findIndex(
      (section) => section.id === updatedItem.id
    );

    // ë²„ê·¸ ë°©ì§€ìš©
    if (targetIndex === -1) {
      console.log('ë§¤ì¹­ë˜ëŠ” ì„¹ì…˜ ì—†ìŒ(í˜„ì¬ í˜ì´ì§€)');
      return;
    }

    // ë¹„ì–´ìˆìˆëŠ” ì„¹ì…˜ ì²˜ë¦¬ëŠ” ìƒê°í•´ë´ì•¼í• ë“¯
    if (updatedItem.rows.length === 0) {
      console.log('ë¹„ì–´ìˆëŠ” ì„¹ì…˜, ', updatedItem);
      this.data.sections[targetIndex] = { ...updatedItem };
      this.changedItem.emit(this.data);
      return;
    }

    // ì„¹ì…˜ ì—…ë°ì´íŠ¸
    console.log('í˜ì´ì§€ ë°ì´í„° ì—…ë°ì´íŠ¸');

    this.data.sections[targetIndex] = { ...updatedItem };
    this.changedItem.emit(this.data);
  }

  createSection(): ISection {
    return {
      id: uuid(),
      rows: [],
    };
  }
}
