import { Component, inject, Input, ViewChild } from '@angular/core';
import { IApp } from '../../interfaces/wix.interface';
import { MatDrawer } from '@angular/material/sidenav';
import { MatDialog } from '@angular/material/dialog';
import { WixViewModel } from '../../viewmodels/wix.viewmodel';
import { filter, Subject, takeUntil, tap } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { MembershipService } from '../../../membership/membership.service';
import { v4 as uuid } from 'uuid';

/**
 * ë¯¸ë¦¬ë³´ê¸° í™”ë©´
 */
@Component({
  selector: 'app-app-preview',
  templateUrl: './app-preview.component.html',
  styleUrl: './app-preview.component.scss',
  standalone: false,
})
export class AppPreviewComponent {
  @Input()
  appData: IApp = {
    id: uuid(),
    name: 'ì´ˆê¸°ë°ì´í„°',
    ownerId: '',
    pages: [],
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  };

  @ViewChild('drawer', { static: true, read: MatDrawer })
  drawer!: MatDrawer;

  selectedAppId!: string;

  membershipService = inject(MembershipService);
  sketchVM = inject(WixViewModel);
  dialog = inject(MatDialog);
  router = inject(Router);
  route = inject(ActivatedRoute);
  destroy$ = new Subject<void>();

  toolbarData: any = {
    data: this.appData?.name ?? 'ë¯¸ë¦¬ë³´ê¸°',
    buttonConfigs: [],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };

  sideToolbarData: any = {
    data: 'ë©”ë‰´',
    buttonConfigs: [
      {
        data: {
          icon: 'close',
          tooltip: 'ë©”ë‰´ ë‹«ê¸°',
        },
        style: {},
        event: () => {
          this.drawer.close();
        },
      },
    ],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };
  pageData: any;

  ngOnInit(): void {
    // this.pageData = this.appData.pages[0];

    const appId = this.route.snapshot.paramMap.get('appId');
    if (!appId) {
      console.log('appId ì•ˆë„˜ì–´ì˜´', appId);
      this.router.navigate(['syncbi/nocode/dashboard']);
      return;
    }
    this.selectedAppId = appId;
    // this.sketchVM.getAppData(appId);

    this.sketchVM.appData$
      .pipe(
        tap((data) => {
          if (!data || data.id !== appId) {
            this.sketchVM.getAppData(appId); // ì„œë²„ ìš”ì²­
          }
        }),
        filter((data) => {
          return !!data;
        }),
        takeUntil(this.destroy$)
      )
      .subscribe((data) => {
        console.log('ì•± ë°ì´í„°, ', data);
        if (!data) return;
        this.appData = data;
        this.toolbarData = {
          ...this.toolbarData,
          data: data.name,
        };

        if (
          this.membershipService.getUser()?.id === this.appData.ownerId
        ) {
          console.log('ë²„íŠ¼ ì´ˆê¸°í™”');

          this.toolbarButtonInit();
        }

        // this.toolbarData.data = data.name;
        if (data.pages.length > 0) {
          this.pageData = data.pages[0];
        }
      });
  }

  toolbarButtonInit() {
    this.toolbarData = {
      ...this.toolbarData,
      buttonConfigs: [
        ...this.toolbarData.buttonConfigs,
        {
          data: {
            icon: 'draw',
            tooltip: 'ë””ìì¸ ëª¨ë“œ',
          },
          style: {},
          event: () => {
            this.router.navigate([
              '/syncbi/nocode/manage/design',
              this.appData.id,
            ]);
          },
        },
        {
          data: {
            icon: 'public',
            tooltip: 'ê²Œì‹œí•˜ê¸°',
          },
          style: {
            label: {
              fontSize: '100px',
            },
          },
          event: () => {
            this.sketchVM.updateAppData(this.appData);
          },
        },
      ],
    };
  }

  onPageChange(page: any) {
    this.pageData = page;
  }
}
