import { Component, EventEmitter, inject, Input, OnDestroy, OnInit, Output, ViewChild, ViewContainerRef } from '@angular/core';
import { IDraggable, IWidget, IWidgetComponent } from '../../interfaces/wix.interface';
import { widgetDesignItemMap, widgetItemMap } from '../../const/widget-item.map';
import { WixDragViewModel } from '../../viewmodels/wix-drag.viewmodel';
import { filter, Subject, take, takeUntil, pipe } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmDialogComponent } from '../../../../shared/dialogs/confirm-dialog/confirm-dialog.component';
import { WIDGET_DEFAULT_DATASET } from '../../const/widget-default-dataset.const';
import { DEFAUT_WIDGET_CONTEXT_MENU, WIDGET_CONTEXT_MENU_MAP } from '../../const/widget-context-menu.const';

@Component({
  selector: 'app-widget-design',
  templateUrl: './widget.component.html',
  styleUrl: './widget.component.scss',
  standalone: false,
})
export class WidgetDesignComponent implements OnInit, OnDestroy, IDraggable {

  @Input()
  data!: IWidget;
  @Input()
  columnId!: string;

  @Output()
  removeWidget = new EventEmitter<IWidget>();

  @Output()
  itemChanged = new EventEmitter<any>();

  @ViewChild('container', {static: true, read: ViewContainerRef})
  container!: ViewContainerRef;

  sketchDragVM = inject(WixDragViewModel);
  dialog = inject(MatDialog);

  destroy$ = new Subject<void>();

  menuData = WIDGET_CONTEXT_MENU_MAP[this.data?.widgetType as keyof typeof WIDGET_CONTEXT_MENU_MAP] ?? DEFAUT_WIDGET_CONTEXT_MENU;

  ngOnInit(): void {
    this.loadComponent();
    this.trackDropEvent();
  }

  loadComponent(){
    this.container.clear();

    const componentType = widgetDesignItemMap[this.data.widgetType as keyof typeof widgetDesignItemMap];
    if(!componentType){
      console.log('ë§¤ì¹­ëŠ” ìœ„ì ¯ì´ ì—†ìŒ');
      return;
    }

    const componentRef = this.container.createComponent(componentType);
    // const inputData = this.data.properties ?? WIDGET_DEFAULT_DATASET[this.data.widgetType];
    // const inputData = this.data.properties;

    componentRef.setInput('properties', this.data.properties);

    (componentRef.instance as any).widgetRemove.pipe(
      takeUntil(this.destroy$)
    ).subscribe( () => {
      this.remove();
    });

    (componentRef.instance as any).updateProperties.pipe(
      takeUntil(this.destroy$),
    ).subscribe((properties: any) => {
      this.data = {
        ...this.data,
        properties:{...properties},
      };
      componentRef.setInput('properties', this.data.properties);
      this.itemChanged.emit(this.data);
    })

  }

  trackDropEvent(){
    this.sketchDragVM.getWidgetDropObservable().pipe(
      filter(eventData => {
        return eventData.widget.id === this.data.id
      }),
      takeUntil(this.destroy$)
    ).subscribe(eventData => {
      console.log('ë“œë˜ê·¸ë“œë¡­ ì´ë²¤íŠ¸ ë°œìƒ');
      this.removeWidget.emit(this.data);
    })
  }

  updateWidgetProperties(properties: { [key: string]: any; }): void {
    throw new Error('Method not implemented.');
  }
  remove(): void {
    this.removeWidget.emit(this.data);
  }

  onDragStart(event: DragEvent): void {
    console.log('ë“œë˜ê·¸ ì‹œì‘');
    event.stopPropagation();

    const eventData = {widget: {...this.data}, draggedId: this.columnId};
    if(!event.dataTransfer){
      console.log('dataTransfer ì—†ìŒ, ', event.dataTransfer);
      return;
    }
    event.dataTransfer.setData('widget', JSON.stringify(eventData));
    const eventDataString = event.dataTransfer.getData('widget');
    console.log('ë°ì´í„° ë‹´ê¹€, ', eventDataString);

  }

  // onMenuSelect(event: any){
  //   console.log('ì´ë²¤íŠ¸ ë°œìƒ, ', event);
  //   (this as any)[event]();
  // }

  // delete(){
  //   const dialogRef = this.dialog.open(ConfirmDialogComponent);
  //   dialogRef.afterClosed().pipe(
  //     take(1),
  //   ).subscribe(result => {
  //     if(!result) return;
  //     this.remove();
  //   })
  // }

  // edit(){

  // }

  ngOnDestroy(): void {
    console.log('ìœ„ì ¯ ì œê±°');
    this.destroy$.next();
  }
}
