import { AfterViewInit, Component, ElementRef, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { PexelsCategory } from '../../enums/pexels-category.enum';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { StaticImageViewModel } from '../../viewmodels/static-image.viewmodel';
import { Subject, takeUntil } from 'rxjs';
import { IPexelsImage } from '../../interfaces/image-source.interface';

@Component({
  selector: 'app-image-gallery-dialog',
  templateUrl: './image-gallery-dialog.component.html',
  styleUrl: './image-gallery-dialog.component.scss',
  standalone: false,
})
export class ImageGalleryDialogComponent
  implements OnInit, AfterViewInit, OnDestroy
{
  toolbarData: any = {
    data: 'ë¬´ë£Œ ì´ë¯¸ì§€',
    buttonConfigs: [
      {
        data: {
          icon: 'close',
        },
        style: {
          label: {
            fontSize: '100px',
          },
        },
        event: () => {
          this.close();
        },
      },
    ],
    style: {
      label: {
        fontSize: '20px',
      },
    },
  };

  categoryList = Object.values(PexelsCategory);
  categories: { key: PexelsCategory; label: string }[] = [
    { key: PexelsCategory.Nature, label: 'ìì—°' },
    { key: PexelsCategory.Animals, label: 'ë™ë¬¼' },
    { key: PexelsCategory.Food, label: 'ìŒì‹' },
    { key: PexelsCategory.Travel, label: 'ì—¬í–‰' },
    { key: PexelsCategory.People, label: 'ì¸ë¬¼' },
    { key: PexelsCategory.Technology, label: 'ê¸°ìˆ ' },
  ];

  images: IPexelsImage[] = [];
  placeholder =
    'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///ywAAAAAAQABAAACAUwAOw==';
  loadedImages = new Set<number>();

  destroy$ = new Subject<void>();

  @ViewChildren('observe')
  imageBoxes!: QueryList<ElementRef>;
  private observer!: IntersectionObserver;

  constructor(
    private dialogRef: MatDialogRef<ImageGalleryDialogComponent>,
    private staticImageVM: StaticImageViewModel
  ) {}

  ngOnInit(): void {
    this.initImage();
    this.staticImageVM.imageSource$
      .pipe(takeUntil(this.destroy$))
      .subscribe((images) => {
        this.images = images;
      });
  }

  ngAfterViewInit(): void {
    console.log('this.imageBoxes.length =', this.imageBoxes.length);
    this.observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          // console.log('ê°ì§€ì¤‘ì„`````````````````````````');

          if (!entry.isIntersecting) {
            console.log('interscting ì•ˆë¨');

            return;
          }
          const box = entry.target as HTMLElement;
          const img = box.querySelector('img');

          const dataSrc = img?.getAttribute('data-src');
          if (!dataSrc) {
            console.log('datasrc ì—†ìŒ');

            return;
          }
          img?.setAttribute('src', dataSrc);
          const idAttr = box.getAttribute('data-id');
          if (idAttr) {
            this.loadedImages.add(+idAttr);
          }
          this.observer.unobserve(box);
        });
      },
      { rootMargin: '50px' }
    );

    this.imageBoxes.changes.subscribe((boxes: QueryList<ElementRef>) => {
      boxes.forEach((el, idx) => {
        el.nativeElement.setAttribute(
          'data-id',
          this.images[idx].id.toString()
        );
        this.observer.observe(el.nativeElement);
      });
    });

    // í˜¹ì‹œ ì´ë¯¸ ì±„ì›Œì ¸ ìˆì„ ìˆ˜ë„ ìˆìœ¼ë‹ˆ ì´ˆê¸° ìƒíƒœë„ í™•ì¸
    if (this.imageBoxes.length > 0) {
      this.imageBoxes.forEach((el, idx) => {
        el.nativeElement.setAttribute(
          'data-id',
          this.images[idx].id.toString()
        );
        this.observer.observe(el.nativeElement);
      });
    }
  }

  close(image?: IPexelsImage) {
    this.dialogRef.close(image);
  }

  onTabChange(event: MatTabChangeEvent) {
    const index = event.index;
    const category = this.categories[index].key;
    this.loadImages(category);
  }

  initImage() {
    this.loadImages(this.categories[0].key);
  }

  loadImages(category: PexelsCategory) {
    // this.staticImageVM.getImagesByCategory(category).pipe(
    //   takeUntil(this.destroy$)
    // ).subscribe((images) => {
    //   this.images = images;
    // })
    this.staticImageVM.getImagesByCategory(category);
  }

  onItemSelect(image: IPexelsImage) {
    console.log('í˜„ì¬ ë°ì´í„°, ', image);

    this.close(image);
  }

  onImageError(image: IPexelsImage) {
    console.log('ì—ëŸ¬ ë°œìƒ, ', image);
  }

  ngOnDestroy(): void {
    this.observer.disconnect();
  }
}
