import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

export interface HistoryCommand {
  execute(): void;
  undo(): void;
  description?: string;
  timestamp?: number;
}

export interface HistoryState {
  canUndo: boolean;
  canRedo: boolean;
  undoCount: number;
  redoCount: number;
  currentIndex: number;
  totalCommands: number;
}

@Injectable({
  providedIn: 'root'
})
export class WixHistoryService {
  
  private undoStack: HistoryCommand[] = [];
  private redoStack: HistoryCommand[] = [];
  private maxHistorySize = 50; // ìµœëŒ€ íˆìŠ¤í† ë¦¬ í¬ê¸°

  private historyStateSubject = new BehaviorSubject<HistoryState>({
    canUndo: false,
    canRedo: false,
    undoCount: 0,
    redoCount: 0,
    currentIndex: -1,
    totalCommands: 0
  });

  public historyState$: Observable<HistoryState> = this.historyStateSubject.asObservable();

  constructor() {
    console.log('âœ… WixHistoryService ì´ˆê¸°í™”');
  }

  /**
   * ìƒˆë¡œìš´ ëª…ë ¹ ì‹¤í–‰ ë° íˆìŠ¤í† ë¦¬ì— ì¶”ê°€
   */
  executeCommand(command: HistoryCommand): void {
    // ëª…ë ¹ ì‹¤í–‰
    command.execute();

    // íƒ€ì„ìŠ¤íƒ¬í”„ ì¶”ê°€
    command.timestamp = Date.now();

    // Undo ìŠ¤íƒì— ì¶”ê°€
    this.undoStack.push(command);

    // ìµœëŒ€ í¬ê¸° ì´ˆê³¼ ì‹œ ì˜¤ë˜ëœ ëª…ë ¹ ì œê±°
    if (this.undoStack.length > this.maxHistorySize) {
      this.undoStack.shift();
    }

    // Redo ìŠ¤íƒ ì´ˆê¸°í™” (ìƒˆë¡œìš´ ëª…ë ¹ ì‹¤í–‰ ì‹œ redo ë¶ˆê°€ëŠ¥)
    this.redoStack = [];

    this.updateState();
    console.log(`ğŸ“ ëª…ë ¹ ì‹¤í–‰: ${command.description || 'ìµëª… ëª…ë ¹'}`);
  }

  /**
   * ì‹¤í–‰ ì·¨ì†Œ
   */
  undo(): void {
    if (!this.canUndo()) {
      console.warn('âš ï¸ Undo ë¶ˆê°€ëŠ¥: íˆìŠ¤í† ë¦¬ê°€ ë¹„ì–´ìˆìŠµë‹ˆë‹¤');
      return;
    }

    const command = this.undoStack.pop();
    if (command) {
      command.undo();
      this.redoStack.push(command);
      this.updateState();
      console.log(`â†©ï¸ Undo: ${command.description || 'ìµëª… ëª…ë ¹'}`);
    }
  }

  /**
   * ë‹¤ì‹œ ì‹¤í–‰
   */
  redo(): void {
    if (!this.canRedo()) {
      console.warn('âš ï¸ Redo ë¶ˆê°€ëŠ¥: Redo ìŠ¤íƒì´ ë¹„ì–´ìˆìŠµë‹ˆë‹¤');
      return;
    }

    const command = this.redoStack.pop();
    if (command) {
      command.execute();
      this.undoStack.push(command);
      this.updateState();
      console.log(`â†ªï¸ Redo: ${command.description || 'ìµëª… ëª…ë ¹'}`);
    }
  }

  /**
   * Undo ê°€ëŠ¥ ì—¬ë¶€
   */
  canUndo(): boolean {
    return this.undoStack.length > 0;
  }

  /**
   * Redo ê°€ëŠ¥ ì—¬ë¶€
   */
  canRedo(): boolean {
    return this.redoStack.length > 0;
  }

  /**
   * íˆìŠ¤í† ë¦¬ ì´ˆê¸°í™”
   */
  clear(): void {
    this.undoStack = [];
    this.redoStack = [];
    this.updateState();
    console.log('ğŸ—‘ï¸ íˆìŠ¤í† ë¦¬ ì´ˆê¸°í™”');
  }

  /**
   * íŠ¹ì • ê°œìˆ˜ë§Œí¼ Undo
   */
  undoMultiple(count: number): void {
    for (let i = 0; i < count && this.canUndo(); i++) {
      this.undo();
    }
  }

  /**
   * íŠ¹ì • ê°œìˆ˜ë§Œí¼ Redo
   */
  redoMultiple(count: number): void {
    for (let i = 0; i < count && this.canRedo(); i++) {
      this.redo();
    }
  }

  /**
   * í˜„ì¬ íˆìŠ¤í† ë¦¬ ëª©ë¡ ì¡°íšŒ
   */
  getUndoHistory(): HistoryCommand[] {
    return [...this.undoStack];
  }

  getRedoHistory(): HistoryCommand[] {
    return [...this.redoStack];
  }

  /**
   * ìƒíƒœ ì—…ë°ì´íŠ¸
   */
  private updateState(): void {
    this.historyStateSubject.next({
      canUndo: this.canUndo(),
      canRedo: this.canRedo(),
      undoCount: this.undoStack.length,
      redoCount: this.redoStack.length,
      currentIndex: this.undoStack.length - 1,
      totalCommands: this.undoStack.length + this.redoStack.length
    });
  }

  /**
   * ìµœëŒ€ íˆìŠ¤í† ë¦¬ í¬ê¸° ì„¤ì •
   */
  setMaxHistorySize(size: number): void {
    this.maxHistorySize = Math.max(1, size);
    
    // í˜„ì¬ íˆìŠ¤í† ë¦¬ê°€ ìµœëŒ€ í¬ê¸°ë¥¼ ì´ˆê³¼í•˜ë©´ ìë¥´ê¸°
    while (this.undoStack.length > this.maxHistorySize) {
      this.undoStack.shift();
    }
    
    this.updateState();
  }

  /**
   * ë””ë²„ê·¸: íˆìŠ¤í† ë¦¬ ì¶œë ¥
   */
  printHistory(): void {
    console.group('ğŸ“š íˆìŠ¤í† ë¦¬ ëª©ë¡');
    console.log('Undo ìŠ¤íƒ:', this.undoStack.map(c => c.description || 'ìµëª…'));
    console.log('Redo ìŠ¤íƒ:', this.redoStack.map(c => c.description || 'ìµëª…'));
    console.groupEnd();
  }
}
