import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { CommonModule } from '@angular/common';
import { IFormGroupData } from '../../../interfaces/form-data.interface';

@Component({
  selector: 'app-basic-form-group',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, MatFormFieldModule, MatInputModule],
  template: `
    <form [formGroup]="formGroup" (submit)="onSubmit()">
      @for (control of formGroupData.controls; track $index) {
        <mat-form-field appearance="outline" style="width: 100%; margin-bottom: 16px;">
          <mat-label>{{ control.label }}</mat-label>
          <input matInput [formControlName]="control.controlName" [type]="control.type || 'text'">
          @if (formGroup.get(control.controlName)?.invalid && formGroup.get(control.controlName)?.touched) {
            <mat-error>
              @for (validator of control.validators; track validator) {
                @if (formGroup.get(control.controlName)?.hasError(validator.validator)) {
                  {{ validator.errorMessage }}
                }
              }
            </mat-error>
          }
        </mat-form-field>
      }
    </form>
  `,
  styles: [`
    form {
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  `]
})
export class BasicFormGroupComponent implements OnInit {
  @Input()
  formGroupData!: IFormGroupData;

  @Output()
  formReady = new EventEmitter<FormGroup>();

  formGroup = new FormGroup({});

  ngOnInit(): void {
    // Form controls ìƒì„±
    this.formGroupData.controls.forEach(control => {
      const validators: any[] = [];
      if (control.validators) {
        control.validators.forEach(v => {
          switch (v.validator) {
            case 'required':
              validators.push(Validators.required);
              break;
            case 'email':
              validators.push(Validators.email);
              break;
          }
        });
      }
      this.formGroup.addControl(
        control.controlName,
        new FormControl(control.defaultValue || '', validators)
      );
    });
    this.formReady.emit(this.formGroup);
  }

  onSubmit() {
    // if (this.formGroup.invalid) {
    //   console.log('í¼ ìœ íš¨ì„± ê²€ì‚¬ ì‹¤íŒ¨');
    //   return;
    // }
    // this.formSubmit.emit(this.formGroup.value);
  }
}
