import { Component } from '@angular/core';
import { WidgetComponentAbstract } from '../../../abstract/widget-component.abstract';

@Component({
  selector: 'app-widget-video',
  templateUrl: './widget-video.component.html',
  styleUrl: './widget-video.component.scss', standalone: false,
})
export class WidgetVideoComponent extends WidgetComponentAbstract {
  
  getDefaultProperties() {
    return {
      url: '',
      autoplay: false
    }
  }

  updateWidgetProperties(properties: { [key: string]: any; }): void {
    this.properties = { ...this.properties, ...properties };
  }
}
