import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { WixHistoryService, HistoryState } from '../../services/wix-history.service';
import { Subject, takeUntil } from 'rxjs';

@Component({
  selector: 'app-history-panel',
  templateUrl: './history-panel.component.html',
  styleUrl: './history-panel.component.scss',
  standalone: false
})
export class HistoryPanelComponent implements OnInit, OnDestroy {
  
  private historyService = inject(WixHistoryService);
  private destroy$ = new Subject<void>();

  historyState: HistoryState = {
    canUndo: false,
    canRedo: false,
    undoCount: 0,
    redoCount: 0,
    currentIndex: -1,
    totalCommands: 0
  };

  undoHistory: any[] = [];
  redoHistory: any[] = [];

  ngOnInit(): void {
    this.historyService.historyState$
      .pipe(takeUntil(this.destroy$))
      .subscribe(state => {
        this.historyState = state;
        this.undoHistory = this.historyService.getUndoHistory();
        this.redoHistory = this.historyService.getRedoHistory();
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  undo(): void {
    this.historyService.undo();
  }

  redo(): void {
    this.historyService.redo();
  }

  clear(): void {
    if (confirm('ëª¨ë“  íˆìŠ¤í† ë¦¬ë¥¼ ì‚­ì œí•˜ì‹œê² ìŠµë‹ˆê¹Œ?')) {
      this.historyService.clear();
    }
  }

  undoMultiple(count: number): void {
    this.historyService.undoMultiple(count);
  }

  redoMultiple(count: number): void {
    this.historyService.redoMultiple(count);
  }

  formatTimestamp(timestamp?: number): string {
    if (!timestamp) return '';
    const date = new Date(timestamp);
    return date.toLocaleTimeString('ko-KR');
  }
}
