import { Component, EventEmitter, Input, Output, ElementRef, ViewChild, AfterViewInit } from '@angular/core';

export interface ResizeEvent {
  width: number;
  height: number;
}

@Component({
  selector: 'app-resize-handler',
  templateUrl: './resize-handler.component.html',
  styleUrls: ['./resize-handler.component.scss'],
  standalone: false,
})
export class ResizeHandlerComponent implements AfterViewInit {
  @Input() style: any = {};
  @Output() resized = new EventEmitter<ResizeEvent>();
  @ViewChild('container', { static: false }) container!: ElementRef;

  isResizing = false;
  startX = 0;
  startY = 0;
  startWidth = 0;
  startHeight = 0;

  ngAfterViewInit() {
    if (this.style.width) {
      this.container.nativeElement.style.width = this.style.width;
    }
    if (this.style.height) {
      this.container.nativeElement.style.height = this.style.height;
    }
  }

  onMouseDown(event: MouseEvent, direction: string): void {
    event.preventDefault();
    event.stopPropagation();
    
    this.isResizing = true;
    this.startX = event.clientX;
    this.startY = event.clientY;
    
    const rect = this.container.nativeElement.getBoundingClientRect();
    this.startWidth = rect.width;
    this.startHeight = rect.height;

    const onMouseMove = (e: MouseEvent) => {
      if (!this.isResizing) return;

      const deltaX = e.clientX - this.startX;
      const deltaY = e.clientY - this.startY;

      let newWidth = this.startWidth;
      let newHeight = this.startHeight;

      if (direction.includes('e')) {
        newWidth = Math.max(50, this.startWidth + deltaX);
      }
      if (direction.includes('w')) {
        newWidth = Math.max(50, this.startWidth - deltaX);
      }
      if (direction.includes('s')) {
        newHeight = Math.max(50, this.startHeight + deltaY);
      }
      if (direction.includes('n')) {
        newHeight = Math.max(50, this.startHeight - deltaY);
      }

      this.container.nativeElement.style.width = `${newWidth}px`;
      this.container.nativeElement.style.height = `${newHeight}px`;

      this.resized.emit({ width: newWidth, height: newHeight });
    };

    const onMouseUp = () => {
      this.isResizing = false;
      document.removeEventListener('mousemove', onMouseMove);
      document.removeEventListener('mouseup', onMouseUp);
    };

    document.addEventListener('mousemove', onMouseMove);
    document.addEventListener('mouseup', onMouseUp);
  }
}
