import { IColumn, ISketchComponent, IWidget } from '../../interfaces/wix.interface';
import { Component, EventEmitter, inject, Input, OnInit, Output } from '@angular/core';
import { IRow } from '../../interfaces/wix.interface';
import { v4 as uuid } from 'uuid';
import { WixDragViewModel } from '../../viewmodels/wix-drag.viewmodel';
import { Subject, takeUntil } from 'rxjs';

@Component({
  selector: 'app-row-design',
  templateUrl: './row.component.html',
  styleUrl: './row.component.scss',
  standalone: false,
})
export class RowDesignComponent implements ISketchComponent<IRow>, OnInit {

  @Input()
  data!: IRow;

  @Input()
  rowIndex!: number;

  @Output()
  changedItem = new EventEmitter<IRow>;

  sketchVM = inject(WixDragViewModel);
  
  // ë¦¬ì‚¬ì´ì¦ˆ ê´€ë ¨
  private resizing = false;
  private resizeColumnIndex = -1;
  private startX = 0;
  private startWidth = 0;

  ngOnInit(): void {
  }

  addItem(type?: string): void {
    throw new Error('Method not implemented.');
  }
  removeItem(id: string): void {
    throw new Error('Method not implemented.');
  }
  onItemChange(updatedItem: any): void {
    console.log('ë°ì´í„° ë³€í™” ê°ì§€, í˜„ì¬ ë¡œìš°');

    const targetIndex = this.data.columns.findIndex(column => column.id === updatedItem.id);

    // íƒ€ê²Ÿ ì»¬ëŸ¼ì„ ì°¾ì§€ ëª»í–ˆì„ ê²½ìš°(ë²„ê·¸ ë°©ì§€ìš©)
    if(targetIndex === -1){
      console.log('ë§¤ì¹­ë˜ëŠ” ì»¬ëŸ¼ ì—†ìŒ');
      return;
    }

    const target = this.data.columns[targetIndex];

    // ì»¬ëŸ¼ì´ ë¹ˆ ë°ì´í„°ì¼ ê²½ìš° ìì› ì •ë¦¬
    if(!updatedItem.widget && (!updatedItem.rows || updatedItem.rows.length === 0)){
      console.log('ë¹ˆ ì»¬ëŸ¼ ì •ë¦¬');

      this.data.columns.splice(targetIndex, 1);
      this.changedItem.emit(this.data);
      return;
    }

    // ê·¸ ì™¸ ë°ì´í„° ë™ê¸°í™” ë¡œì§
    this.data.columns[targetIndex] = updatedItem;
    this.changedItem.emit(this.data);

  }
  getGridTemplateColumns(): string {
    const numColumns = this.data.columns.length;
    
    if (numColumns === 0) return '1fr';
    
    // ê° Columnì´ ìµœì†Œ 200px, ìµœëŒ€ ê· ë“± ë¶„ë°°
    return `repeat(${numColumns}, minmax(200px, 1fr))`;
  }

  onDragOver(event: DragEvent, position: string, targetId: string): void {
    event.preventDefault();
    event.stopPropagation();
    const target = event.target as HTMLElement;
    target.classList.add('hovered');
  }

  onDragLeave(event: DragEvent): void {
    event.preventDefault();
    event.stopPropagation();
    const target = event.target as HTMLElement;
    target.classList.remove('hovered');
  }

  onDrop(event: DragEvent, position: 'left' | 'right', targetId: string): void {
    event.preventDefault();
    event.stopPropagation();
    const target = event.target as HTMLElement;
    target.classList.remove('hovered');

    if(!event.dataTransfer){
      console.log('dataTransfer ì—†ìŒ, ë¡œìš°');
      return;
    }

    const eventDataString = event.dataTransfer.getData('widget');
    if (!eventDataString) {
      console.log('ì´ë²¤íŠ¸ ë°ì´í„° ì•ˆ ë„˜ì–´ì˜´', eventDataString ?? 'ë¹„ì–´ìˆìŒ');
      return;
    }

    const eventData = JSON.parse(eventDataString);
    console.log('ì•„ì´í…œ ë“œë¡­', eventData);

    this.sketchVM.emitWidgetDrop(eventData.draggedId, eventData.widget);

    this.insertColumn(eventData.widget, position, targetId);
  }

  insertColumn(widget: IWidget, position: 'left' | 'right', targetId: string) {
    const targetIndex = this.data.columns.findIndex(col => col.id === targetId);
    if (targetIndex === -1) return;

    const newColumn: IColumn = { id: uuid(), widget };

    if (position === 'left') {
      this.data.columns.splice(targetIndex, 0, newColumn);
    } else {
      this.data.columns.splice(targetIndex + 1, 0, newColumn);
    }
  }

  onResizeStart(event: MouseEvent, columnIndex: number): void {
    event.preventDefault();
    event.stopPropagation();
    
    this.resizing = true;
    this.resizeColumnIndex = columnIndex;
    this.startX = event.clientX;
    
    const columnElement = (event.target as HTMLElement).closest('.column-container') as HTMLElement;
    if (columnElement) {
      this.startWidth = columnElement.offsetWidth;
    }
    
    // ì „ì—­ ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì¶”ê°€
    document.addEventListener('mousemove', this.onResizeMove.bind(this));
    document.addEventListener('mouseup', this.onResizeEnd.bind(this));
    
    // ì»¤ì„œ ë³€ê²½
    document.body.style.cursor = 'col-resize';
  }
  
  private onResizeMove(event: MouseEvent): void {
    if (!this.resizing) return;
    
    const deltaX = event.clientX - this.startX;
    const newWidth = Math.max(200, this.startWidth + deltaX); // ìµœì†Œ 200px
    
    // Grid template ì—…ë°ì´íŠ¸ (ê°„ë‹¨í•œ êµ¬í˜„)
    const columnElement = document.querySelectorAll('.column-container')[this.resizeColumnIndex] as HTMLElement;
    if (columnElement) {
      columnElement.style.width = `${newWidth}px`;
      columnElement.style.minWidth = `${newWidth}px`;
    }
  }
  
  private onResizeEnd(event: MouseEvent): void {
    if (!this.resizing) return;
    
    this.resizing = false;
    this.resizeColumnIndex = -1;
    
    // ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì œê±°
    document.removeEventListener('mousemove', this.onResizeMove.bind(this));
    document.removeEventListener('mouseup', this.onResizeEnd.bind(this));
    
    // ì»¤ì„œ ë³µì›
    document.body.style.cursor = '';
  }

}
