import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IRow, ISection } from '../../interfaces/wix.interface';
import { v4 as uuid } from 'uuid';

@Component({
  selector: 'app-section',
  templateUrl: './section.component.html',
  styleUrl: './section.component.scss', standalone: false,
})
export class SectionComponent {
  @Input() data!: ISection;

  @Output()
  changedItem = new EventEmitter<ISection>();

  addRow(rowIndex: number){
    const newRow = this.createRow();
    this.data.rows.splice(rowIndex + 1, 0, newRow);
    this.changedItem.emit(this.data);
  }

  onItemChange(updatedItem: any){
    const targetIndex = this.data.rows.findIndex(row => row.id === updatedItem.id);

    if(targetIndex === -1){
      console.log('ë§¤ì¹­ë˜ëŠ” í–‰ ì—†ìŒ');
      return;
    }

    this.data.rows[targetIndex] = {...updatedItem};
    this.changedItem.emit(this.data);
  }

  createRow(): IRow{
    return {
      id: uuid(),
      type: 'row',
      columns: []
    }
  }
}
