@Directive()
export abstract class DataAbstract implements OnDestroy {
  protected destroy$ = new Subject<void>();

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * ê°ì²´ ê¹Šì€ ë³µì‚¬
   */
  protected deepCopy<T>(obj: T): T {
    return JSON.parse(JSON.stringify(obj));
  }

  /**
   * ë°°ì—´ì—ì„œ ì•„ì´í…œ ì œê±°
   */
  protected removeItem<T>(array: T[], item: T): T[] {
    const index = array.indexOf(item);
    if (index > -1) {
      array.splice(index, 1);
    }
    return array;
  }
}
