import { Injectable, inject } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class UtilService {
  private serviceReadySubject = new BehaviorSubject<boolean>(false);
  public serviceReady$ = this.serviceReadySubject.asObservable();
  private snackBar = inject(MatSnackBar);

  constructor() {}

  serviceReady(): void {
    this.serviceReadySubject.next(true);
  }

  /**
   * UUID ìƒì„± (v4)
   */
  generateUUID(): string {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      const r = Math.random() * 16 | 0;
      const v = c === 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

  /**
   * ìŠ¤ë‚µë°” ë©”ì‹œì§€ í‘œì‹œ
   */
  openSnackBar(message: string, type: 'success' | 'error' | 'info' = 'info', duration: number = 3000): void {
    this.snackBar.open(message, 'ë‹«ê¸°', {
      duration,
      horizontalPosition: 'center',
      verticalPosition: 'bottom',
      panelClass: [`snackbar-${type}`]
    });
  }
}
