import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { WixAutoSaveService, AutoSaveState, SaveStatus } from '../../services/wix-auto-save.service';
import { Subject, interval, takeUntil } from 'rxjs';

@Component({
  selector: 'app-save-status-indicator',
  templateUrl: './save-status-indicator.component.html',
  styleUrl: './save-status-indicator.component.scss',
  standalone: false
})
export class SaveStatusIndicatorComponent implements OnInit, OnDestroy {
  
  private autoSaveService = inject(WixAutoSaveService);
  private destroy$ = new Subject<void>();

  saveState: AutoSaveState = {
    status: SaveStatus.IDLE,
    isPending: false
  };

  SaveStatus = SaveStatus;
  autoSaveEnabled = true;
  lastSavedText = '';

  ngOnInit(): void {
    // ì €ì¥ ìƒíƒœ êµ¬ë…
    this.autoSaveService.saveState$
      .pipe(takeUntil(this.destroy$))
      .subscribe(state => {
        this.saveState = state;
        this.updateLastSavedText();
      });

    // 1ì´ˆë§ˆë‹¤ ë§ˆì§€ë§‰ ì €ì¥ ì‹œê°„ ì—…ë°ì´íŠ¸
    interval(1000)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.updateLastSavedText();
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  updateLastSavedText(): void {
    this.lastSavedText = this.autoSaveService.getLastSavedFormatted();
  }

  toggleAutoSave(): void {
    this.autoSaveEnabled = !this.autoSaveEnabled;
    this.autoSaveService.setAutoSaveEnabled(this.autoSaveEnabled);
  }

  getStatusIcon(): string {
    switch (this.saveState.status) {
      case SaveStatus.SAVING:
        return 'cloud_upload';
      case SaveStatus.SAVED:
        return 'cloud_done';
      case SaveStatus.ERROR:
        return 'cloud_off';
      default:
        return 'cloud_queue';
    }
  }

  getStatusText(): string {
    if (this.saveState.isPending) {
      return 'ì €ì¥ ëŒ€ê¸° ì¤‘...';
    }

    switch (this.saveState.status) {
      case SaveStatus.SAVING:
        return 'ì €ì¥ ì¤‘...';
      case SaveStatus.SAVED:
        return `ì €ì¥ë¨ (${this.lastSavedText})`;
      case SaveStatus.ERROR:
        return 'ì €ì¥ ì‹¤íŒ¨';
      default:
        return 'ì €ì¥ ì•ˆë¨';
    }
  }

  getStatusClass(): string {
    switch (this.saveState.status) {
      case SaveStatus.SAVING:
        return 'saving';
      case SaveStatus.SAVED:
        return 'saved';
      case SaveStatus.ERROR:
        return 'error';
      default:
        return 'idle';
    }
  }
}
