import { Directive, EventEmitter, inject, Input, OnInit, Output, Type } from "@angular/core";
import { IWidgetComponent, IWidgetUpdateDialog } from "../interfaces/wix.interface";
import { MatDialog, MatDialogRef } from "@angular/material/dialog";

@Directive()
export abstract class WidgetComponentAbstract implements IWidgetComponent, OnInit {

  @Input()
  properties: any;

  @Output()
  widgetRemove = new EventEmitter<void>();

  dialog = inject(MatDialog);

  ngOnInit(): void {
    // í•„ìš”ì‹œ ê¸°ë³¸ ì†ì„± ì´ˆê¸°í™”
  }

  remove(){
    this.widgetRemove.emit();
  }

  /**
   * ìœ„ì ¯ ì—…ë°ì´íŠ¸ ë‹¤ì´ì–¼ë¡œê·¸ë¥¼ ì—´ê³  ê·¸ ì¸ìŠ¤í„´ìŠ¤ë¥¼ ë°˜í™˜í•˜ëŠ” ë©”ì„œë“œ
   */
  openUpdateDialog<T extends IWidgetUpdateDialog>(type: Type<T>): MatDialogRef<T> {
    const dialogRef = this.dialog.open(type, {
      data: {
        properties: this.properties
      }
    });
    return dialogRef;
  }

  abstract getDefaultProperties(): any;

  abstract updateWidgetProperties(properties: { [key: string]: any; }): void;

}
