import { Injectable, inject } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { DebugLoggerService } from '../../../shared/services/debug-logger.service';
import { CryptoUtil } from '../../../core/utils/crypto.util';

export interface WixUser {
  id: string;
  username: string;
  email: string;
  role: 'admin' | 'designer' | 'user';
  permissions: string[];
}

@Injectable({
  providedIn: 'root'
})
export class WixAuthService {
  private debugLogger = inject(DebugLoggerService);
  
  // environmentì—ì„œ ì•”í˜¸í™”ëœ ì¸ì¦ ì •ë³´ë¥¼ ë³µí˜¸í™”í•˜ì—¬ ì‚¬ìš©
  private readonly ADMIN_USERNAME = CryptoUtil.decrypt(environment.auth.username);
  private readonly ADMIN_PASSWORD = CryptoUtil.decrypt(environment.auth.password);
  
  private currentUserSubject = new BehaviorSubject<WixUser | null>(null);
  private isWixLoggedInSubject = new BehaviorSubject<boolean>(false);
  
  public currentUser$: Observable<WixUser | null> = this.currentUserSubject.asObservable();
  public isWixLoggedIn$: Observable<boolean> = this.isWixLoggedInSubject.asObservable();

  constructor() {
    // Wix ë¡œê·¸ì¸ ìƒíƒœ ë³µì›
    const savedUser = localStorage.getItem('wixUser');
    const savedToken = localStorage.getItem('wixToken');
    
    if (savedUser && savedToken) {
      try {
        this.currentUserSubject.next(JSON.parse(savedUser));
        this.isWixLoggedInSubject.next(true);
      } catch (error) {
        this.clearWixAuth();
      }
    }
  }

  get currentUser(): WixUser | null {
    return this.currentUserSubject.value;
  }

  get isWixLoggedIn(): boolean {
    return this.isWixLoggedInSubject.value;
  }

  /**
   * Wix ë¹Œë” ë¡œê·¸ì¸
   */
  async wixLogin(username: string, password: string): Promise<boolean> {
    try {
      this.debugLogger.info('WixAuth', 'ğŸ” ë¡œê·¸ì¸ ì‹œë„ ì‹œì‘');
      this.debugLogger.info('WixAuth', `ğŸ“ ì…ë ¥ëœ username: ${username}`);
      
      this.debugLogger.info('WixAuth', `âœ… í™˜ê²½ë³€ìˆ˜ username: ${this.ADMIN_USERNAME}`);
      this.debugLogger.info('WixAuth', `âœ… í™˜ê²½ë³€ìˆ˜ password: ${this.ADMIN_PASSWORD}`);
      
      // ì„ì‹œ ë¡œê·¸ì¸ ì²˜ë¦¬ (ê°œë°œìš©)
      await new Promise(resolve => setTimeout(resolve, 800));
      
      // í™˜ê²½ë³€ìˆ˜ ê°’ê³¼ ë¹„êµí•˜ì—¬ ì¸ì¦
      this.debugLogger.info('WixAuth', `ğŸ” Username ì¼ì¹˜: ${username === this.ADMIN_USERNAME}`);
      this.debugLogger.info('WixAuth', `ğŸ” Password ì¼ì¹˜: ${password === this.ADMIN_PASSWORD}`);
      
      if (username !== this.ADMIN_USERNAME || password !== this.ADMIN_PASSWORD) {
        this.debugLogger.error('WixAuth', 'âŒ ë¡œê·¸ì¸ ì‹¤íŒ¨: ì•„ì´ë”” ë˜ëŠ” ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŒ');
        return false;
      }
      
      // ì¸ì¦ ì„±ê³µ ì‹œ ì‚¬ìš©ì ì •ë³´
      const user: WixUser = {
        id: '1',
        username: username,
        email: `${username}@example.com`,
        role: 'admin',
        permissions: ['create', 'read', 'update', 'delete', 'publish']
      };

      const token = 'wix_token_' + Date.now(); // ì‹¤ì œë¡œëŠ” ì„œë²„ì—ì„œ ë°›ì•„ì˜´

      this.currentUserSubject.next(user);
      this.isWixLoggedInSubject.next(true);
      localStorage.setItem('wixUser', JSON.stringify(user));
      localStorage.setItem('wixToken', token);
      
      this.debugLogger.success('WixAuth', 'âœ… ë¡œê·¸ì¸ ì„±ê³µ!');
      return true;
    } catch (error) {
      this.debugLogger.error('WixAuth', `âŒ ë¡œê·¸ì¸ ì¤‘ ì˜¤ë¥˜ ë°œìƒ: ${error}`);
      return false;
    }
  }

  /**
   * íŠ¹ì • ê¶Œí•œ í™•ì¸
   */
  hasPermission(permission: string): boolean {
    const user = this.currentUser;
    return user ? user.permissions.includes(permission) : false;
  }

  /**
   * ì—­í•  í™•ì¸
   */
  hasRole(role: string): boolean {
    const user = this.currentUser;
    return user ? user.role === role : false;
  }

  /**
   * Wix ë¡œê·¸ì•„ì›ƒ
   */
  wixLogout(): void {
    this.currentUserSubject.next(null);
    this.isWixLoggedInSubject.next(false);
    this.clearWixAuth();
  }

  /**
   * ì¸ì¦ ì •ë³´ ì‚­ì œ
   */
  private clearWixAuth(): void {
    localStorage.removeItem('wixUser');
    localStorage.removeItem('wixToken');
  }

  /**
   * í† í° ê°€ì ¸ì˜¤ê¸°
   */
  getToken(): string | null {
    return localStorage.getItem('wixToken');
  }
}
