import { Subject } from "rxjs";
import { IWidget } from "../interfaces/wix.interface";

export class WixDragViewModel {
  widgetDropSubject = new Subject<{draggedId: string, widget: IWidget}>();

  emitWidgetDrop(draggedId: string, widget: IWidget){
    this.widgetDropSubject.next({draggedId, widget});
  }

  getWidgetDropObservable(){
    return this.widgetDropSubject.asObservable();
  }
}
