import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { WixAuthService } from '../modules/wix/services/wix-auth.service';

@Injectable({
  providedIn: 'root'
})
export class WixAuthGuard implements CanActivate {
  constructor(
    private wixAuthService: WixAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    
    if (this.wixAuthService.isWixLoggedIn) {
      // ê¶Œí•œ ì²´í¬ê°€ í•„ìš”í•œ ê²½ìš°
      const requiredPermission = route.data['permission'] as string;
      if (requiredPermission && !this.wixAuthService.hasPermission(requiredPermission)) {
        this.router.navigate(['/wix/access-denied']);
        return false;
      }
      
      // ì—­í•  ì²´í¬ê°€ í•„ìš”í•œ ê²½ìš°
      const requiredRole = route.data['role'] as string;
      if (requiredRole && !this.wixAuthService.hasRole(requiredRole)) {
        this.router.navigate(['/wix/access-denied']);
        return false;
      }
      
      return true;
    }

    // ë¡œê·¸ì¸ë˜ì–´ ìˆì§€ ì•Šìœ¼ë©´ ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ë¦¬ë‹¤ì´ë ‰íŠ¸
    return this.router.createUrlTree(['/wix/login'], {
      queryParams: { returnUrl: state.url }
    });
  }
}
