import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IColumn, IRow } from '../../interfaces/wix.interface';
import { v4 as uuid } from 'uuid';

@Component({
  selector: 'app-row',
  templateUrl: './row.component.html',
  styleUrl: './row.component.scss', standalone: false,
})
export class RowComponent {
  @Input() data!: IRow;

  @Output()
  changedItem = new EventEmitter<IRow>();

  addColumn(columnIndex: number){
    const newColumn = this.createColumn();
    this.data.columns.splice(columnIndex + 1, 0, newColumn);
    this.changedItem.emit(this.data);
  }

  onItemChange(updatedItem: any){
    const targetIndex = this.data.columns.findIndex(col => col.id === updatedItem.id);

    if(targetIndex === -1){
      console.log('ë§¤ì¹­ë˜ëŠ” ì—´ ì—†ìŒ');
      return;
    }

    this.data.columns[targetIndex] = {...updatedItem};
    this.changedItem.emit(this.data);
  }

  createColumn(): IColumn{
    return {
      id: uuid()
    }
  }
}
